<template>
  <div class="sum" >
    <div class="topButton">
      <el-button type="primary" @click="showAdd()" style="padding: 12px 15px;float: left">新建本月记录</el-button>
    </div>
    <!-- 表格 -->
    <div class="list">
      <el-table
        :data="sumList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        style="width: 100%;border-color:#3a8ee6"
        :default-sort = "{prop: 'num', order: 'ascending'}"
        border
        :header-cell-style="{background:'#eef1f6',color:'#333333'}"
        @current-change="handleCurrentsChange"
      >
        <el-table-column
          prop="num"
          label="序号"
          align="center"
          width="150"
        >
        </el-table-column>
        <el-table-column
          prop="time"
          label="时间"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="sum"
          label="合计"
          align="center">
        </el-table-column>
        <el-table-column
          prop="operate"
          label="操作"
          align="center">
          <template scope="scope" >
            <el-button type="primary" @click="showDetail(scope.row.time)" style="padding: 12px 15px">查看</el-button>
            <el-button type="danger"  @click="showDelete()" style="padding: 12px 15px">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 40]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="sumList.length"
        ></el-pagination>
      </div>
    </div>

    <!-- 添加弹框 -->
    <div v-if="flag" class="addSum" ref="addSum" :style="addSumStyle">
      <div  class="addTitle">
        <h2>新增临时工资单</h2>
        <i class="el-icon-close" @click="closeAdd()"></i>
      </div>

      <div class="add">
        <div class="topButton">
          <el-button type="primary" @click="AddForm()" style="padding: 12px 15px;float: left;position: relative;left:73px;top:-40px">增加记录</el-button>
        </div>
        <el-form ref="form" :model="addList" label-width="80px">
          <el-form-item label="选择时间:" style="position: relative;left: 72px;top:-40px;size: 15px">
            <el-select v-model="addList.time" placeholder="请选择时间" style="width: 150px;position: relative;left: -286px;">
              <el-option  v-for="item in createdateList"
                          :label="item.name"
                          :value="item.name"
                          >
              </el-option>
            </el-select>
          </el-form-item>

            <span style="font-weight: bold;color:black;font-size: 15px;position:relative;left: -80px;top:-40px">姓名</span >
            <span style="font-weight: bold;color:black;font-size: 15px;margin: 50px;position:relative;top:-40px">工资合计</span>
            <span style="font-weight: bold;color:black;font-size: 15px;margin: 50px;position:relative;top:-40px">代扣个税</span>
            <span style="font-weight: bold;color:black;font-size: 15px;margin: 50px;position:relative;top:-40px">实发金额</span>
            <span style="font-weight: bold;color:black;font-size: 15px;margin: 50px;position:relative;top:-40px">备注</span>
            <el-form :inline="true" class="demo-form-inline" v-for="(item, i) in FormArr" :key="i">

                       <el-select v-model="item.name" placeholder="请选择员工" style="width: 150px;margin: 10px;top:-40px">
                         <el-option  v-for="item in employerList"
                                     :label="item.name"
                                     :value="item.name">
                         </el-option>
                       </el-select>
                       <el-input v-model="item.sum" @blur="blur()" style="width: 150px;margin-right: 10px;top:-40px"></el-input>
                       <el-input v-model="item.tax" style="width: 150px;margin-right: 10px;top:-40px"></el-input>
                       <el-input v-model="item.money" style="width: 150px;margin-right: 10px;top:-40px"></el-input>
                       <el-input v-model="item.tips" style="width: 150px;margin-right: 10px;top:-40px;"></el-input>
                       <i class="el-icon-delete-solid" @click="Delete(item.index)" style="position:relative;top:-40px"></i>
     
            </el-form>


          <div class="addButton">
            <el-form-item>
              <el-button type="primary" @click="addSum()" style="padding: 12px 15px">添加</el-button>
              <el-button @click="addRest()" style="padding: 12px 15px">重置</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>

    <!-- 删除弹框 -->
    <div class="delete" v-if="flag3">
      <div class="title">
        <span>删除提示</span>
        <i class="el-icon-close" @click="closeDelete()"></i>
      </div>
      <div class="main">
        <i class="el-icon-warning"></i>
        <span>您确定要删除此临时工资信息吗？</span>
      </div>
      <div class="deleteButton">
        <el-button  type="danger" @click="deleteInfo()" style="padding: 12px 15px">确定</el-button>
        <el-button  @click="closeDelete()" style="padding: 12px 15px">取消</el-button>
      </div>
    </div>

    <div class="popContainer" v-if="flag"></div>
    <div class="popContainer" v-if="flag2"></div>
    <div class="popContainer" v-if="flag3"></div>

  </div>
</template>

<script>
    export default {
        data() {
            return {
                sumList: [],
                flag: false,
                flag2: false,
                flag3: false,
                currentPage: 1, //初始页
                pagesize: 10,
                flag: false,
                addSumStyle:{
                    height:'370px'
                },
                addList: [
                    {
                        id:"",
                        time:"",
                        sum: ""
                    }
                ],
                employerList:[

                ],
                createdateList:[

                ],
                taxrateList:[

                ],
                FormArr: [
                    {
                        index: 0,
                        value: ''
                    }
                ],
                deleteList: [
                ],

            };
        },
        mounted(){
            // console.log('mounted:',document.getElementById("addSum"));
        },
        created(){
            let _this = this;
            this.$axios
                .get("/getAllSum")
                .then(function(res) {
                    if (res.data) {
                        _this.sumList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
            this.$axios
                .get("/getAllCreatedate")
                .then(function(res) {
                    if (res.data) {
                        _this.createdateList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
            this.$axios
                .get("/getAllEmployer")
                .then(function(res) {
                    if (res.data) {
                        _this.employerList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
            this.$axios
                .get("/getAllTaxrate")
                .then(function(res) {
                    if (res.data) {
                        _this.taxrateList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
        },
        inject: ["reload"],
        methods: {
            addSum() {
                let _this = this
                    for(let i =0;i<this.FormArr.length;i++){
                          this.$axios
                              .post('/addSum', {
                                  time: this.addList.time,
                                  name: this.FormArr[i].name,
                                  secondsum: this.FormArr[i].sum,
                                  tax: this.FormArr[i].tax,
                                  sumpayable: this.FormArr[i].money,
                                  note: this.FormArr[i].tips
                              })
                              .then(successResponse => {
                                  if (successResponse.data.code === 200) {
                                      this.flag = !this.flag;
                                      this.reload();
                                      this.$message({
                                          message: '添加成功！！！',
                                          type: 'success',
                                          center: true
                                      });
                                  } else {
                                      this.reload();
                                      this.$message.error('该员工已有记录，请勿重复添加！');
                                  }
                              })
                              .catch(failResponse => {
                                  this.reload();
                                  this.$message.error('添加信息填写不正确！');
                              });
                    }
            },

            deleteInfo() {
                let _this = this
                this.$axios
                    .delete('/deleteSum', {
                        data: {
                            id:this.deleteList.id
                        }
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            this.flag3 = !this.flag3;
                            this.reload();
                            this.$message({
                                message: '删除成功！！！',
                                type: 'success',
                                center: true
                            });
                        }else {
                            this.reload();
                            this.$message.error('删除失败！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('删除失败！');
                    });
            },

            blur(){
                let tax =0;let tax1 =0;
                for(let i =0;i<this.FormArr.length;i++){
                    for(let j =0;j<this.taxrateList.length;j++){
                        if(this.FormArr[i].sum<this.taxrateList[j].min){
                            tax1 =0;
                        }else if(this.FormArr[i].sum>=this.taxrateList[j].min && this.FormArr[i].sum<=this.taxrateList[j].max){
                            if(j==0){
                                tax1 = (this.FormArr[i].sum - this.taxrateList[j].min)*this.taxrateList[j].taxrate;
                            }else{
                                tax1 = (this.FormArr[i].sum - this.taxrateList[j-1].max)*this.taxrateList[j].taxrate;
                            }
                        }else if(this.FormArr[i].sum>=this.taxrateList[j].max){
                            if(j==0){
                                tax1 = (this.taxrateList[j].max - this.taxrateList[j].min)*this.taxrateList[j].taxrate;
                            }else{
                                tax1 = (this.taxrateList[j].max - this.taxrateList[j-1].max)*this.taxrateList[j].taxrate;
                            }
                        }
                        tax=tax+tax1;
                    }
                    this.$set(this.FormArr[i],"tax",tax);
                    this.$set(this.FormArr[i],"money",this.FormArr[i].sum-this.FormArr[i].tax);
                }
            },

            showAdd() {
                this.flag = !this.flag;
                this.addList = [];
            },

            showDetail(time) {
                //指定跳转地址
                this.$router.push({
                    path: "/tSalaryDetail",
                    query: { time: time}
                })
            },

            showDelete() {
                let _this = this
                this.flag3 = !this.flag3;
            },

            closeAdd() {
                this.flag = !this.flag;
                this.$message("操作已取消");
                this.reload();
            },

            AddForm () {
                this.FormArr.push({
                    index: this.FormArr.length,
                    value: ''
                })
                let height= this.$refs.addSum.offsetHeight;
                this.addSumStyle.height =(height +60)+'px';
                console.log(this.FormArr)
            },
            Delete (index) {
                this.FormArr.splice(index, 1)
                for (let i in this.FormArr) {
                    this.FormArr[i].index = i
                }
                let height= this.$refs.addSum.offsetHeight;
                this.addSumStyle.height =(height -60)+'px';
            },

            addRest() {
                this.addList = [];
                this.FormArr = [
                    {
                        index: 0,
                        value: ''
                    }
                ];
            },

            closeDelete() {
                this.flag3 = !this.flag3;
                this.$message("操作已取消");
                this.reload();
            },

            handleCurrentsChange(val) {
                this.currentRow = val;
                this.addList = val;
                this.deleteList = val;
            },
            handleSizeChange: function(size) {
                this.pagesize = size;
                // console.log(this.pagesize)  //每页下拉显示数据
            },
            handleCurrentChange: function(currentPage) {
                this.currentPage = currentPage;
                // console.log(this.currentPage)  //点击第几页
            }
        },

    }
</script>
<style>
  .topButton {
    width: 100%;
    height: 50px;
    line-height: 50px;
    padding-bottom: 10px;
  }
  .el-submenu .el-menu-item:hover {
    background-color: rgb(232, 243, 255);
  }
  .list {
    position: relative;
  }
  .page {
    position: absolute;
    right: 0;
    bottom: -50px;
  }

  .el-table--border:after,
  .el-table--group:after,
  .el-table:before {
    background-color: #3a8ee6;
  }

  .el-table--border,
  .el-table--group {
    border-color: #3a8ee6;
  }

  .addSum {
    position: absolute;
    z-index: 1001;
    width: 1000px;
    /*height: 400px;*/
    max-height:800px;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  .addSum .addTitle {
    height: 50px;
    width: 100%;
    text-align: center;
    line-height: 30px;
    /* border-bottom: 1px solid #dcdfe6; */
    color: #409eff;
    text-shadow: 2px 2px 2px #ccc;
  }
  .addSum .addTitle i {
    position: relative;
    top: -55px;
    right: -470px;
    color: black;
    font-size: 20px;
  }
  .addSum .el-form-item__label{
    color: black;
    font-weight: bold;
  }
  .addSum .add {
    margin-right: 30px;
    margin-top: 30px;
  }
  .addSum .addButton {
    margin-left:-15px;
    margin-top: -10px;
  }
  .addSum .addButton .el-button {
    width: 80px;
  }
  .addSum .el-select {
    width: 280px;
  }

  .el-form-item__label{
    font-size: 15px;
  }
  .delete {
    position: absolute;
    z-index: 1001;
    width: 400px;
    height: 180px;
    border: 1px solid #dcdfe6;
    left: 0;
    top: -50px;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
  }
  .title {
    width: 100%;
    height: 45px;
    line-height: 45px;
  }
  .title span {
    float: left;
    margin-left: 20px;
  }
  .delete .title i {
    float: right;
    margin-top: 10px;
    margin-right: 10px;
  }
  .delete .main {
    height: 60px;
    width: 100%;
    line-height: 50px;
    margin-top: 15px;
    margin-left: 0px;
  }
  .delete .main i {
    color: #e6a23c;
    font-size: 20px;
    margin-right: 10px;
  }
  .delete .main span {
    margin-bottom: 5px;
  }
  .delete .deleteButton {
    float: right;
    margin-right: 20px;
    margin-top: 5px;
  }
  .popContainer {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 500;
  }
</style>



