<template>
  <div class="workday">
  <!-- 表格 -->
  <div class="list">
    <el-table
      :data="salaryList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
      style="width: 60%;border-color:#3a8ee6;position:relative ;left:300px"
      :default-sort = "{prop: 'id', order: 'ascending'}"
      border
      :header-cell-style="{background:'#eef1f6',color:'#333333'}"
      @current-change="handleCurrentsChange"
    >
      <el-table-column
        prop="num"
        label="序号"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="detail"
        label="详情"
        align="center">
      </el-table-column>
      <el-table-column
        prop="amount"
        label="金额"
        sortable
        align="center">
      </el-table-column>
    </el-table>

    <!-- 分页 -->
    <div class="page">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5, 10, 20, 40]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="salaryList.length"
        style="position:relative ;right:350px"
      ></el-pagination>
    </div>
  </div>
  </div>
</template>
<script>

    export default {
        data() {
            return {
                userobjectid:this.$route.query.userobjectid,
                salaryList: [],
                currentPage: 1, //初始页
                pagesize: 10,
                flag: false,
            };
        },
        components: {

        },
        computed: {
            eid() {
                return this.$store.state.employer.id;
            }
        },
        created(){
            let _this = this;
            this.$axios
                .get("/getUserById?id="+this.userobjectid)
                .then(function(res) {
                    if (res.data) {
                        _this.salaryList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
        },
        methods: {
            handleCurrentsChange(val) {
                this.currentRow = val;

                this.changeDept = val;
                console.log(this.changeDept)
                // this.changeList = []
            },
            handleSizeChange: function(size) {
                this.pagesize = size;
                // console.log(this.pagesize)  //每页下拉显示数据
            },
            handleCurrentChange: function(currentPage) {
                this.currentPage = currentPage;
                // console.log(this.currentPage)  //点击第几页
            }
        },
    }
</script>
<style>
  .list {
    position: relative;
  }
  .page {
    /* margin-top: 20px;
      margin-left: 800px; */
    position: absolute;
    right: 0;
    bottom: -50px;
  }
  .el-table--border:after,
  .el-table--group:after,
  .el-table:before {
    background-color: #3a8ee6;
  }

  .el-table--border,
  .el-table--group {
    border-color: #3a8ee6;
  }

  .el-table{
    position: center;
  }
</style>




