<template>
  <div class="awards">
    <div class="topButton">
      <el-button type="primary" @click="showAdd()" style="padding: 12px 15px;float: left">添加</el-button>
    </div>
    <!-- 表格 -->
    <div class="list">
      <el-table
        :data="awardsList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        style="width: 100%;border-color:#3a8ee6"
        :default-sort = "{prop: 'num', order: 'ascending'}"
        border
        :header-cell-style="{background:'#eef1f6',color:'#333333'}"
        @current-change="handleCurrentsChange"
      >
        <el-table-column
          prop="num"
          label="序号"
          sortable
          align="center"
          width="150"
        >
        </el-table-column>
        <el-table-column
          prop="updatedate"
          label="更新时间"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="ename"
          label="员工姓名"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="time"
          label="每月迟到时间限制（小时）"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="money"
          label="奖励金额"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="operate"
          label="操作"
          align="center">
          <template scope="scope" >
            <el-button type="warning" @click="showChange()" style="padding: 12px 15px">修改</el-button>
            <el-button type="danger"  @click="showDelete()" style="padding: 12px 15px">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 40]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="awardsList.length"
        ></el-pagination>
      </div>
    </div>

    <!-- 添加弹框 -->
    <div v-if="flag" class="addAwards" ref="addAwards" :style="addAwardsStyle">
      <div class="title">
        <h2>新增奖励规则</h2>
        <i class="el-icon-close" @click="closeAdd()"></i>
      </div>

      <div class="add">
        <div class="topButton">
          <el-button type="primary" @click="AddForm()" style="padding: 12px 15px;float: left;position: relative;left:73px;top:-20px">增加奖励种类</el-button>
        </div>
        <el-form ref="form" :model="addList" label-width="80px">
          <el-form-item label="员工姓名" style="position: relative;left: 90px;top:-25px">
            <el-select v-model="addList.ename" placeholder="请选择员工姓名" style="position: relative;left:-55px;width:150px">
              <el-option  v-for="item in employerList"
                          :label="item.name"
                          :value="item.name">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="每月迟到时间限制" style="position: relative;left: 90px;top:-40px">
            <el-input v-model="addList.time" style="position: relative;top:20px;left:-55px;width:150px"></el-input>
          </el-form-item>

          <span style="font-weight: bold;color:black;font-size: 15px;position:relative;left: -25px;top:-50px">奖金种类</span >
          <span style="font-weight: bold;color:black;font-size: 15px;margin: 50px;position:relative;top:-50px;left:35px">奖金金额</span>
          <el-form :inline="true" class="demo-form-inline" v-for="(item, i) in FormArr" :key="i">

            <el-select v-model="item.category" placeholder="请选择奖金种类" style="width: 150px;margin: 10px;top:-40px">
              <el-option  v-for="item in awardcategoryList"
                          :label="item.name"
                          :value="item.name">
              </el-option>
            </el-select>
            <el-input v-model="item.money" style="width: 150px;margin-right: 10px;top:-40px"></el-input>
            <i class="el-icon-delete-solid" @click="Delete(item.index)" style="position:relative;top:-40px"></i>
               
          </el-form>


          <el-form-item label="更新时间" style="position: relative;left: 90px;bottom:25px">
            <el-date-picker
              v-model="addList.updatedate"
              type="date"
              placeholder="选择日期"
              style="width:150px;position:relative;left:-58px"
              >
            </el-date-picker>
          </el-form-item>

          <div class="addButton">
            <el-form-item>
              <el-button type="primary" @click="addAwards()" style="padding: 12px 15px">添加</el-button>
              <el-button @click="addRest()" style="padding: 12px 15px">重置</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>

    <!-- 修改弹框 -->
    <div v-if="flag2" class="changeAwards"  ref="changeAwards" :style="changeAwardsStyle">
      <div class="title">
        <h2>修改奖励规则</h2>
        <i class="el-icon-close" @click="closeChange()"></i>
      </div>

      <div class="change">
        <div class="topButton">
          <el-button type="primary" @click="AddForm1()" style="padding: 12px 15px;float: left;position: relative;left:73px;top:-20px">增加奖励种类</el-button>
        </div>
        <el-form ref="form" :model="changeList" label-width="80px">
          <el-form-item label="员工姓名" style="position: relative;left: 90px;top:-25px">
            <el-select v-model="changeList.ename" placeholder="请选择员工姓名" style="position: relative;left:-55px;width:150px">
              <el-option  v-for="item in employerList"
                          :label="item.name"
                          :value="item.name">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="每月迟到时间限制" style="position: relative;left: 90px;top:-40px">
            <el-input v-model="changeList.time" style="position: relative;top:20px;left:-55px;width:150px"></el-input>
          </el-form-item>

          <span style="font-weight: bold;color:black;font-size: 15px;position:relative;left: -25px;top:-50px">奖金种类</span >
          <span style="font-weight: bold;color:black;font-size: 15px;margin: 50px;position:relative;top:-50px;left:35px">奖金金额</span>
          <el-form :inline="true" class="demo-form-inline" v-for="(item, i) in changeList.award" :key="i">

            <el-select v-model="item.category" placeholder="请选择奖金种类" style="width: 150px;margin: 10px;top:-40px">
              <el-option  v-for="item in awardcategoryList"
                          :label="item.name"
                          :value="item.name">
              </el-option>
            </el-select>
            <el-input v-model="item.award" style="width: 150px;margin-right: 10px;top:-40px"></el-input>
            <i class="el-icon-delete-solid" @click="Delete1(item.index)" style="position:relative;top:-40px"></i>
               
          </el-form>


          <el-form-item label="更新时间" style="position: relative;left: 90px;bottom:25px">
            <el-date-picker
              v-model="changeList.updatedate"
              type="date"
              placeholder="选择日期"
              style="width:150px;position:relative;left:-58px"
            >
            </el-date-picker>
          </el-form-item>

          <div class="saveButton">
            <el-form-item>
              <el-button type="primary" @click="changeAwards()" style="padding: 12px 15px">修改</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>

    <!-- 删除弹框 -->
    <div class="delete" v-if="flag3">
      <div class="title">
        <span>删除提示</span>
        <i class="el-icon-close" @click="closeDelete()"></i>
      </div>
      <div class="main">
        <i class="el-icon-warning"></i>
        <span>您确定要删除此奖金规则吗？</span>
      </div>
      <div class="deleteButton">
        <el-button  type="danger" @click="deleteInfo()" style="padding: 12px 15px">确定</el-button>
        <el-button  @click="closeDelete()" style="padding: 12px 15px">取消</el-button>
      </div>
    </div>

    <div class="popContainer" v-if="flag"></div>
    <div class="popContainer" v-if="flag2"></div>
    <div class="popContainer" v-if="flag3"></div>

  </div>
</template>

<script>
    export default {
        data() {
            return {
                awardsList: [],
                flag: false,
                flag2: false,
                flag3: false,
                currentPage: 1, //初始页
                pagesize: 10,
                flag: false,
                addAwardsStyle:{
                    height:'510px'
                },
                changeAwardsStyle:{
                    height:'580px'
                },
                employerList:[

                ],
                awardcategoryList:[

                ],
                addList: [
                    {
                        id:"",
                        updatedate:"",
                        ename:"",
                        time:"",
                        money: "",
                        category:""
                    }
                ],
                FormArr: [
                    {
                        index: 0,
                        value: ''
                    }
                ],
                changeList: [
                    {
                        id:"",
                        updatedate:"",
                        ename:"",
                        time:"",
                        money: "",
                        award:[
                            {
                                award:"",
                                category:"",
                            }
                        ]
                    }
                ],
                deleteList: [
                    {
                        id:"",
                        updatedate:"",
                        ename:"",
                        time:"",
                        money: ""
                    }
                ],

            };
        },
        mounted(){

        },
        created(){
            let _this = this;
            this.$axios
                .get("/getAllAwards")
                .then(function(res) {
                    if (res.data) {
                        _this.awardsList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
            this.$axios
                .get("/getAllEmployer")
                .then(function(res) {
                    if (res.data) {
                        _this.employerList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
            this.$axios
                .get("/getAllAwardcategory")
                .then(function(res) {
                    if (res.data) {
                        _this.awardcategoryList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
        },
        inject: ["reload"],
        methods: {
            addAwards() {
                let _this = this
                for(let i = 0;i<this.FormArr.length;i++) {
                    let date =new Date(this.addList.updatedate)
                    this.updatedate =date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate();
                    this.$axios
                        .post('/addAwards', {
                            ename: this.addList.ename,
                            time: this.addList.time,
                            updatedate: this.updatedate,
                            category: this.FormArr[i].category,
                            money: this.FormArr[i].money
                        })
                        .then(successResponse => {
                            if (successResponse.data.code === 200) {
                                this.flag = !this.flag;
                                this.reload();
                                this.$message({
                                    message: '添加成功！！！',
                                    type: 'success',
                                    center: true
                                });
                            } else if (successResponse.data.code === 100) {
                                this.reload();
                                this.$message.error('该员工已存在此奖励种类，请勿重复添加！');
                            }
                        })
                        .catch(failResponse => {
                            this.reload();
                            this.$message.error('添加信息填写不正确！');
                        });
                }
            },

            changeAwards() {
                let _this = this
                this.$axios
                    .delete('/deleteAwards', {
                        data: {
                            id:this.changeList.id
                        }
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            for(let i = 0;i<this.changeList.award.length;i++) {
                                let date = new Date(this.changeList.updatedate)
                                this.updatedate = date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate();
                                this.$axios
                                    .post('/addAwards', {
                                        ename: this.changeList.ename,
                                        time: this.changeList.time,
                                        updatedate: this.updatedate,
                                        category: this.changeList.award[i].category,
                                        money: this.changeList.award[i].award
                                    })
                                    .then(successResponse => {
                                        if (successResponse.data.code === 200) {
                                            this.flag = !this.flag;
                                            this.reload();
                                            this.$message({
                                                message: '修改成功！！！',
                                                type: 'success',
                                                center: true
                                            });
                                        } else if (successResponse.data.code === 100) {
                                            this.reload();
                                            this.$message.error('该员工已存在此奖励种类，请勿重复添加！');
                                        }
                                    })
                                    .catch(failResponse => {
                                        this.reload();
                                        this.$message.error('修改后信息填写不正确！');
                                    });
                            }
                        }else {
                            this.reload();
                            this.$message.error('修改失败！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('修改失败！');
                    });
            },
            deleteInfo() {
                let _this = this
                this.$axios
                    .delete('/deleteAwards', {
                        data: {
                            id:this.deleteList.id
                        }
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            this.flag3 = !this.flag3;
                            this.reload();
                            this.$message({
                                message: '删除成功！！！',
                                type: 'success',
                                center: true
                            });
                        }else {
                            this.reload();
                            this.$message.error('删除失败！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('删除失败！');
                    });
            },


            showAdd() {
                this.flag = !this.flag;
                this.addList = [
                ];
            },

            showChange() {

                this.flag2 = !this.flag2;
            },
            showDelete() {

                this.flag3 = !this.flag3;
            },

            closeAdd() {
                this.flag = !this.flag;
                this.$message("操作已取消");
                this.reload();
            },

            AddForm () {
                this.FormArr.push({
                    index: this.FormArr.length,
                    value: ''
                })
                let height= this.$refs.addAwards.offsetHeight;
                this.addAwardsStyle.height =(height +60)+'px';
                console.log(this.FormArr)
            },

            AddForm1 () {
                this.changeList.award.push({
                    index: this.changeList.award.length,
                    value: ''
                })
                let height= this.$refs.changeAwards.offsetHeight;
                this.changeAwardsStyle.height =(height +60)+'px';
                console.log(this.changeList.award)
            },

            Delete (index) {
                this.FormArr.splice(index, 1)
                for (let i in this.FormArr) {
                    this.FormArr[i].index = i
                }
                let height= this.$refs.addAwards.offsetHeight;
                this.addAwardsStyle.height =(height -60)+'px';
            },

            Delete1 (index) {
                this.changeList.award.splice(index, 1)
                for (let i in this.changeList.award) {
                    this.changeList.award[i].index = i
                }
                let height= this.$refs.changeAwards.offsetHeight;
                this.changeAwardsStyle.height =(height -60)+'px';
            },

            addRest() {
                this.addList = [];
                this.FormArr = [
                    {
                        index: 0,
                        value: ''
                    }
                ];
            },

            closeChange() {
                this.flag2 = !this.flag2;
                this.$message("操作已取消");
                this.reload();
            },
            closeDelete() {
                this.flag3 = !this.flag3;
                this.$message("操作已取消");
                this.reload();
            },

            handleCurrentsChange(val) {
                this.currentRow = val;
                this.addList = val;
                this.changeList = val;
                this.deleteList = val;
            },
            handleSizeChange: function(size) {
                this.pagesize = size;
                // console.log(this.pagesize)  //每页下拉显示数据
            },
            handleCurrentChange: function(currentPage) {
                this.currentPage = currentPage;
                // console.log(this.currentPage)  //点击第几页
            }
        },

    }
</script>
<style>
  .topButton {
    width: 100%;
    height: 50px;
    line-height: 50px;
    padding-bottom: 10px;
  }
  .el-submenu .el-menu-item:hover {
    background-color: rgb(232, 243, 255);
  }
  .list {
    position: relative;
  }
  .page {
    position: absolute;
    right: 0;
    bottom: -50px;
  }

  .el-table--border:after,
  .el-table--group:after,
  .el-table:before {
    background-color: #3a8ee6;
  }

  .el-table--border,
  .el-table--group {
    border-color: #3a8ee6;
  }

  .addAwards {
    position: absolute;
    z-index: 1001;
    width: 500px;
    /*height: 600px;*/
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  .addAwards .title {
    height: 50px;
    width: 100%;
    text-align: center;
    line-height: 30px;
    /* border-bottom: 1px solid #dcdfe6; */
    color: #409eff;
    text-shadow: 2px 2px 2px #ccc;
  }
  .addAwards .title i {
    position: relative;
    top: -55px;
    right: -230px;
    color: black;
    font-size: 20px;
  }
  .addAwards .el-form-item__label{
    color: black;
    font-weight: bold;
  }
  .addAwards .add {
    margin-right: 30px;
    margin-top: 30px;
  }
  .addAwards .addButton {
    margin-left:-15px;
    margin-top: -20px;
  }
  .addAwards .addButton .el-button {
    width: 80px;
  }
  .addAwards .el-select {
    width: 280px;
  }
  .changeAwards {
    position: absolute;
    z-index: 1001;
    width: 500px;
    /*height: 600px;*/
    left: 0;
    top: 0px;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  .changeAwards .title {
    height: 50px;
    width: 100%;
    text-align: center;
    line-height: 30px;
    /* border-bottom: 1px solid #dcdfe6; */
    color: #409eff;
    text-shadow: 2px 2px 2px #ccc;
  }
  .changeAwards .title i {
    position: relative;
    top: -55px;
    right: -230px;
    color: black;
    font-size: 20px;
  }
  .changeAwards .el-form-item__label{
    color: black;
    font-weight: bold;
  }
  .changeAwards .change {
    margin-right: 30px;
    margin-top: 30px;
  }

  .changeAwards .saveButton {
    margin-left:-50px;
    margin-top: -10px;
  }

  .changeAwards .saveButton .el-button {
    width: 80px;
  }
  .changeAwards .el-select {
    width: 280px;
  }
  .delete {
    position: absolute;
    z-index: 1001;
    width: 400px;
    height: 180px;
    border: 1px solid #dcdfe6;
    left: 0;
    top: -50px;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
  }
  .title {
    width: 100%;
    height: 45px;
    line-height: 45px;
  }
  .title span {
    float: left;
    margin-left: 20px;
  }
  .delete .title i {
    float: right;
    margin-top: 10px;
    margin-right: 10px;
  }
  .delete .main {
    height: 60px;
    width: 100%;
    line-height: 50px;
    margin-top: 15px;
    margin-left: 0px;
  }
  .delete .main i {
    color: #e6a23c;
    font-size: 20px;
    margin-right: 10px;
  }
  .delete .main span {
    margin-bottom: 5px;
  }
  .delete .deleteButton {
    float: right;
    margin-right: 20px;
    margin-top: 5px;
  }
  .popContainer {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 500;
  }
</style>



