<template>
  <div class="awardcategory">
    <div class="topButton">
      <el-button type="primary" @click="showAdd()" style="padding: 12px 15px;float: left">添加</el-button>
    </div>
    <!-- 表格 -->
    <div class="list">
      <el-table
        :data="awardcategoryList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        style="width: 100%;border-color:#3a8ee6"
        :default-sort = "{prop: 'num', order: 'ascending'}"
        border
        :header-cell-style="{background:'#eef1f6',color:'#333333'}"
        @current-change="handleCurrentsChange"
      >
        <el-table-column
          prop="num"
          label="序号"
          align="center"
          sortable
          width="150"
        >
        </el-table-column>
        <el-table-column
          prop="name"
          label="奖金种类"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="shown"
          label="是否显示在工资表"
          sortable
          align="center">
          <template scope="scope">
            <el-tag
              :type="scope.row.shown === '否' ? 'danger' : 'success'"
              disable-transitions>{{scope.row.shown}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          prop="operate"
          label="操作"
          align="center">
          <template scope="scope" >
            <el-button type="warning" @click="showChange()" style="padding: 12px 15px">修改</el-button>
            <el-button type="danger"  @click="showDelete()" style="padding: 12px 15px">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 40]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="awardcategoryList.length"
        ></el-pagination>
      </div>
    </div>

    <!-- 添加弹框 -->
    <div v-if="flag" class="addAwardcategory">
      <div class="title">
        <h2>新增奖金种类</h2>
        <i class="el-icon-close" @click="closeAdd()"></i>
      </div>

      <div class="add">

        <el-form ref="form" :model="addList" label-width="80px">
          <el-form-item label="奖金种类">
            <el-input v-model="addList.name" placeholder="请输入奖金种类名称"></el-input>
          </el-form-item>

          <el-form-item label="是否显示在工资表">
              <template>
              <el-radio  v-model="addShown" label="否" style="position: relative;top:20px" aria-checked="true"><span style="font-weight: bold">不显示</span></el-radio>
              <el-radio  v-model="addShown" label="是" style="position: relative;top:20px"><span style="font-weight: bold">显示</span></el-radio>
              </template>
          </el-form-item>

          <div class="addButton">
            <el-form-item>
              <el-button type="primary" @click="addAwardcategory()" style="padding: 12px 15px">添加</el-button>
              <el-button @click="addRest()" style="padding: 12px 15px">重置</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>

    <!-- 修改弹框 -->
    <div v-if="flag2" class="changeAwardcategory">
      <div class="title">
        <h2>修改奖金种类</h2>
        <i class="el-icon-close" @click="closeChange()"></i>
      </div>

      <div class="change">
        <el-form ref="form" :model="changeList" label-width="80px">
          <el-form-item label="奖金种类">
            <el-input v-model="changeList.name"></el-input>
          </el-form-item>

          <el-form-item label="是否显示在工资表">
            <template>
              <el-radio  v-model="changeList.shown" label="否" style="position: relative;top:20px"><span style="font-weight: bold">不显示</span></el-radio>
              <el-radio  v-model="changeList.shown" label="是" style="position: relative;top:20px"><span style="font-weight: bold">显示</span></el-radio>
            </template>
          </el-form-item>

          <div class="saveButton">
            <el-form-item>
              <el-button type="primary" @click="changeAwardcategory()" style="padding: 12px 15px">修改</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>

    <!-- 删除弹框 -->
    <div class="delete" v-if="flag3">
      <div class="title">
        <span>删除提示</span>
        <i class="el-icon-close" @click="closeDelete()"></i>
      </div>
      <div class="main">
        <i class="el-icon-warning"></i>
        <span>您确定要删除此奖金种类吗？</span>
      </div>
      <div class="deleteButton">
        <el-button  type="danger" @click="deleteInfo()" style="padding: 12px 15px">确定</el-button>
        <el-button  @click="closeDelete()" style="padding: 12px 15px">取消</el-button>
      </div>
    </div>

    <div class="popContainer" v-if="flag"></div>
    <div class="popContainer" v-if="flag2"></div>
    <div class="popContainer" v-if="flag3"></div>

  </div>
</template>

<script>
    export default {
        data() {
            return {
                addShown:"否",
                awardcategoryList: [],
                flag: false,
                flag2: false,
                flag3: false,
                currentPage: 1, //初始页
                pagesize: 10,
                flag: false,
                radio: '0',
                addList: [
                    {
                        id:"",
                        name:"",
                        shown:""
                    }
                ],
                changeList: [
                    {
                        id:"",
                        name:"",
                        shown:""
                    }
                ],
                deleteList: [
                    {
                        id:"",
                        name:"",
                        shown:""
                    }
                ],

            };
        },

        created(){
            let _this = this;
            this.$axios
                .get("/getAllAwardcategory")
                .then(function(res) {
                    if (res.data) {
                        _this.awardcategoryList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
        },
        inject: ["reload"],
        methods: {
            addAwardcategory() {
                let _this = this;
                if(this.addShown==="否"){
                    this.shown =0;
                }else if (this.addShown==="是"){
                    this.shown =1;
                }
                this.$axios
                    .post('/addAwardcategory', {
                        name: this.addList.name,
                        shown: this.shown
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            this.flag = !this.flag;
                            this.reload();
                            this.$message({
                                message: '添加成功！！！',
                                type: 'success',
                                center: true
                            });
                        } else {
                            this.reload();
                            this.$message.error('添加失败！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('添加信息填写不正确！');
                    });
            },

            changeAwardcategory() {
                let _this = this;
                if(this.changeList.shown==="否"){
                    this.shown =0;
                }else if (this.changeList.shown==="是"){
                    this.shown =1;
                }
                this.$axios
                    .put('/updateAwardcategory', {
                        id: this.changeList.id,
                        name: this.changeList.name,
                        shown:this.shown
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            this.flag2 = !this.flag2;
                            this.reload();
                            this.$message({
                                message: '修改成功！！！',
                                type: 'success',
                                center: true
                            });
                        } else {
                            this.reload();
                            this.$message.error('修改失败！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('修改后信息填写不正确！');
                    });
            },
            deleteInfo() {
                let _this = this
                this.$axios
                    .put('/deleteAwardcategory', {
                            id:this.deleteList.id
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            this.flag3 = !this.flag3;
                            this.reload();
                            this.$message({
                                message: '删除成功！！！',
                                type: 'success',
                                center: true
                            });
                        }else {
                            this.reload();
                            this.$message.error('删除失败！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('删除失败！');
                    });
            },


            showAdd() {
                this.flag = !this.flag;
                this.addList = [
                    {
                    shown :"0"
                    },
                ];

            },

            showChange() {

                this.flag2 = !this.flag2;
            },
            showDelete() {

                this.flag3 = !this.flag3;
            },

            closeAdd() {
                this.flag = !this.flag;
                this.$message("操作已取消");
                this.reload();
            },

            addRest() {
                    this.addShown="否",
                    this.addList = [
                    ]
            },

            closeChange() {
                this.flag2 = !this.flag2;
                this.$message("操作已取消");
                this.reload();
            },
            closeDelete() {
                this.flag3 = !this.flag3;
                this.$message("操作已取消");
                this.reload();
            },

            handleCurrentsChange(val) {
                this.currentRow = val;
                this.addList = val;
                this.changeList = val;
                this.deleteList = val;
            },
            handleSizeChange: function(size) {
                this.pagesize = size;
                // console.log(this.pagesize)  //每页下拉显示数据
            },
            handleCurrentChange: function(currentPage) {
                this.currentPage = currentPage;
                // console.log(this.currentPage)  //点击第几页
            }
        },

    }
</script>
<style>
  .topButton {
    width: 100%;
    height: 50px;
    line-height: 50px;
    padding-bottom: 10px;
  }
  .el-submenu .el-menu-item:hover {
    background-color: rgb(232, 243, 255);
  }
  .list {
    position: relative;
  }
  .page {
    position: absolute;
    right: 0;
    bottom: -50px;
  }

  .el-table--border:after,
  .el-table--group:after,
  .el-table:before {
    background-color: #3a8ee6;
  }

  .el-table--border,
  .el-table--group {
    border-color: #3a8ee6;
  }

  .addAwardcategory {
    position: absolute;
    z-index: 1001;
    width: 400px;
    height: 350px;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  .addAwardcategory .title {
    height: 50px;
    width: 100%;
    text-align: center;
    line-height: 30px;
    /* border-bottom: 1px solid #dcdfe6; */
    color: #409eff;
    text-shadow: 2px 2px 2px #ccc;
  }
  .addAwardcategory .title i {
    position: relative;
    top: -55px;
    right: -170px;
    color: black;
    font-size: 20px;
  }
  .addAwardcategory .el-form-item__label{
    color: black;
    font-weight: bold;
  }
  .addAwardcategory .add {
    margin-right: 30px;
    margin-top: 30px;
  }
  .addAwardcategory .addButton {
    margin-left:-15px;
    margin-top: 30px;
  }
  .addAwardcategory .addButton .el-button {
    width: 80px;
  }
  .addAwardcategory .el-select {
    width: 280px;
  }
  .changeAwardcategory{
    position: absolute;
    z-index: 1001;
    width: 400px;
    height: 350px;
    /* border:1px solid #dcdfe6; */
    left: 0;
    top: 0px;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  .changeAwardcategory .title {
    height: 50px;
    width: 100%;
    text-align: center;
    line-height: 30px;
    /* border-bottom: 1px solid #dcdfe6; */
    color: #409eff;
    text-shadow: 2px 2px 2px #ccc;
  }
  .changeAwardcategory .title i {
    position: relative;
    top: -55px;
    right: -170px;
    color: black;
    font-size: 20px;
  }
  .changeAwardcategory .el-form-item__label{
    color: black;
    font-weight: bold;
  }
  .changeAwardcategory .change {
    margin-right: 30px;
    margin-top: 30px;
  }

  .changeAwardcategory .saveButton {
    margin-left:-20px;
    margin-top: 30px;
  }

  .changeAwardcategory .saveButton .el-button {
    width: 80px;
  }
  .changeAwardcategory .el-select {
    width: 280px;
  }
  .delete {
    position: absolute;
    z-index: 1001;
    width: 400px;
    height: 180px;
    border: 1px solid #dcdfe6;
    left: 0;
    top: -50px;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
  }
  .title {
    width: 100%;
    height: 45px;
    line-height: 45px;
  }
  .title span {
    float: left;
    margin-left: 20px;
  }
  .delete .title i {
    float: right;
    margin-top: 10px;
    margin-right: 10px;
  }
  .delete .main {
    height: 60px;
    width: 100%;
    line-height: 50px;
    margin-top: 15px;
    margin-left: 0px;
  }
  .delete .main i {
    color: #e6a23c;
    font-size: 20px;
    margin-right: 10px;
  }
  .delete .main span {
    margin-bottom: 5px;
  }
  .delete .deleteButton {
    float: right;
    margin-right: 20px;
    margin-top: 5px;
  }
  .popContainer {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 500;
  }
</style>



