        <el-menu
          :default-active="$route.path"
          background-color="#424a5d"
          text-color="#fff"
          active-text-color="#ffd04b"
          router
        >
          <el-menu-item index="/aIndex" >
            <i class="el-icon-s-order" style="font-size: 25px"></i>
            <span slot="title" style="font-size: 20px">管理菜单&emsp;&emsp;</span>
