          <el-menu-item index="/salaryManagement" style="padding-left: 0px">
            <i class="el-icon-edit-outline" style="font-size: 20px"></i>
            <span slot="title" style="font-size: 15px">工资管理&emsp;&emsp;</span>
          </el-menu-item>
