            </el-menu-item-group>
          </el-submenu>
          <el-menu-item index="/taxRateManagement" style="padding-left: 0px">
            <i class="el-icon-s-data" style="font-size: 20px"></i>
            <span slot="title" style="font-size: 15px">税率管理&emsp;&emsp;</span>
          </el-menu-item>
