<template>
  <div class="aways">
    <div class="top">
      <br/>
    </div>
    <!-- 表格 -->
    <div class="list">
      <el-table
        :data="awaysList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        style="width: 100%;border-color:#3a8ee6"
        :default-sort = "{prop: 'num', order: 'ascending'}"
        border
        :header-cell-style="{background:'#eef1f6',color:'#333333'}"
        @current-change="handleCurrentsChange"
      >
        <el-table-column
          prop="num"
          label="序号"
          align="center"
          sortable
          width="150"
        >
        </el-table-column>
        <el-table-column
          prop="ename"
          label="员工姓名"
          sortable
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="cutmoney"
          label="旷工扣款金额"
          sortable
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="time"
          label="时间"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="status"
          label="状态"
          align="center"
          sortable>
          <template scope="scope">
            <el-tag
              :type="scope.row.status === '未提交' ? 'primary' : 'success'"
              disable-transitions>{{scope.row.status}}</el-tag>
          </template>

        </el-table-column>

      </el-table>
      <!-- 分页 -->
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 40]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="awaysList.length"
        ></el-pagination>
      </div>
    </div>

  </div>
</template>

<script>
    export default {
        data() {
            return {
                awaysList: [],
                currentPage: 1, //初始页
                pagesize: 10,
                flag: false,
            };
        },
        components: {

        },
        created(){
            let _this = this;
            this.$axios
                .get("/getAllAways")
                .then(function(res) {
                    if (res.data) {
                        _this.awaysList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
        },
        methods: {
            handleCurrentsChange(val) {
                this.currentRow = val;

                this.changeDept = val;
                console.log(this.changeDept)
                // this.changeList = []
            },
            handleSizeChange: function(size) {
                this.pagesize = size;
                // console.log(this.pagesize)  //每页下拉显示数据
            },
            handleCurrentChange: function(currentPage) {
                this.currentPage = currentPage;
                // console.log(this.currentPage)  //点击第几页
            }
        },

    }
</script>
<style>

  .list {
    position: relative;
  }
  .page {
    position: absolute;
    right: 0;
    bottom: -50px;
  }

  .el-table--border:after,
  .el-table--group:after,
  .el-table:before {
    background-color: #3a8ee6;
  }

  .el-table--border,
  .el-table--group {
    border-color: #3a8ee6;
  }
</style>





