import Vue from 'vue'
import Router from 'vue-router'
// 导入刚才编写的组件
import AIndex from '@/components/home/AIndex'
import EIndex from '@/components/home/EIndex'
import ALogin from '@/components/ALogin'
import ELogin from '@/components/ELogin'
import Login from '@/components/Login'
import Home from '@/components/Home'
import SalaryManagement from '@/page/SalaryManagement'
import TSalaryManagement from '@/page/TSalaryManagement'
import SalaryManageDetail from '@/page/SalaryManageDetail'
import TSalaryDetail from '@/page/TSalaryDetail'
import AccountantInfoManagement from '@/page/AccountantInfoManagement'
import EmpInfoManagement from '@/page/EmpInfoManagement'
import TaxRateManagement from '@/page/TaxRateManagement'
import AbsenceFineAmount from '@/page/AbsenceFineAmount'
import AbsenceFineRule from '@/page/AbsenceFineRule'
import BonusRuleManagement from '@/page/BonusRuleManagement'
import LeftOvertimeManagement from '@/page/LeftOvertimeManagement'
import SocialSecManagement from '@/page/SocialSecManagement'
import BonusTypeManagement from '@/page/BonusTypeManagement'
import AllowTypeManagement from '@/page/AllowTypeManagement'
import FineTypeManagement from '@/page/FineTypeManagement'
import ELeftOvertimeManagement from '@/page/ELeftOvertimeManagement'
import ESalaryManagement from '@/page/ESalaryManagement'
import ESalaryDetail from '@/page/ESalaryDetail'
import EmpSalaryInfo from '@/page/EmpSalaryInfo'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    // 下面都是固定的写法
    {
      path: '/',
      name: 'login',
      component: Login
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
      meta:{
        // 页面标题title
        title: '登录首页'
      }
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      // home页面并不需要被访问
      redirect: '/aIndex',
      children: [
          {
            path: '/aIndex',
            name: 'AIndex',
            component: AIndex,
            meta: {
              requireAuth: true,
              title: '管理员首页',
              type:'管理员'
            }
          },
        {
          path: '/eIndex',
          name: 'EIndex',
          component: EIndex,
          meta: {
            requireAuth: true,
            title: '员工首页',
            type:'员工'
          }
        },
        {
          path: '/salaryManagement',
          name: 'SalaryManagement',
          component: SalaryManagement,
          meta: {
            requireAuth: true,
            title: '工资管理',
            type:'管理员',
            header: '工资管理'
          }
        },
        {
          path: '/tSalaryManagement',
          name: 'TSalaryManagement',
          component: TSalaryManagement,
          meta: {
            requireAuth: true,
            title: '临时工资管理',
            type:'管理员',
            header: '临时工资管理'
          }
        },
        {
          path: '/accountantInfoManagement',
          name: 'AccountantInfoManagement',
          component: AccountantInfoManagement,
          meta: {
            requireAuth: true,
            title: '管理员信息管理',
            type:'管理员',
            header: '管理员信息管理'
          }
        },
        {
          path: '/empInfoManagement',
          name: 'EmpInfoManagement',
          component: EmpInfoManagement,
          meta: {
            requireAuth: true,
            title: '员工信息管理',
            type:'管理员',
            header: '员工信息管理'
          }
        },
        {
          path: '/taxRateManagement',
          name: 'TaxRateManagement',
          component: TaxRateManagement,
          meta: {
            requireAuth: true,
            title: '税率管理',
            type:'管理员',
            header: '税率管理'
          }
        },
        {
          path: '/socialSecManagement',
          name: 'SocialSecManagement',
          component: SocialSecManagement,
          meta: {
            requireAuth: true,
            title: '社保管理',
            type:'管理员',
            header: '社保管理'
          }
        },
        {
          path: '/bonusTypeManagement',
          name: 'BonusTypeManagement',
          component: BonusTypeManagement,
          meta: {
            requireAuth: true,
            title: '奖金种类管理',
            type:'管理员',
            header: '奖金种类管理'
          }
        },
        {
          path: '/allowTypeManagement',
          name: 'AllowTypeManagement',
          component: AllowTypeManagement,
          meta: {
            requireAuth: true,
            title: '补贴种类管理',
            type:'管理员',
            header: '补贴种类管理'
          }
        },
        {
          path: '/fineTypeManagement',
          name: 'FineTypeManagement',
          component: FineTypeManagement,
          meta: {
            requireAuth: true,
            title: '扣款种类管理',
            type:'管理员',
            header: '扣款种类管理'
          }
        },
        {
          path: '/bonusRuleManagement',
          name: 'BonusRuleManagement',
          component: BonusRuleManagement,
          meta: {
            requireAuth: true,
            title: '奖金规则管理',
            type:'管理员',
            header:'出勤管理 / 奖金规则管理'
          }
        },
        {
          path: '/absenceFineRule',
          name: 'AbsenceFineRule',
          component: AbsenceFineRule,
          meta: {
            requireAuth: true,
            title: '旷工扣款规则',
            type:'管理员',
            header:'出勤管理 / 旷工扣款规则'
          }
        },
        {
          path: '/leftOvertimeManagement',
          name: 'LeftOvertimeManagement',
          component: LeftOvertimeManagement,
          meta: {
            requireAuth: true,
            title: '请假加班管理',
            type:'管理员',
            header:'出勤管理 / 请假加班管理'
          }
        },
        {
          path: '/absenceFineAmount',
          name: 'AbsenceFineAmount',
          component: AbsenceFineAmount,
          meta: {
            requireAuth: true,
            title: '旷工扣款金额',
            type:'管理员',
            header:'出勤管理 / 旷工扣款金额'
          }
        },
        {
          path: '/eLeftOvertimeManagement',
          name: 'ELeftOvertimeManagement',
          component: ELeftOvertimeManagement,
          meta: {
            requireAuth: true,
            title: '请假加班记录',
            type:'员工',
            header:'请假加班记录',
            showName:'YES'
          }
        },
        {
          path: '/eSalaryManagement',
          name: 'ESalaryManagement',
          component: ESalaryManagement,
          meta: {
            requireAuth: true,
            title: '员工工资查询',
            type:'员工',
            header:'员工工资查询',
            showName:'YES'
          }
        },
        {
          path: '/eSalaryDetail',
          name: 'ESalaryDetail',
          component: ESalaryDetail,
          meta: {
            requireAuth: true,
            title: '员工工资详情',
            type:'员工',
            header:'员工工资详情',
            showName:'YES'
          }
        },
        {
          path: '/tSalaryDetail',
          name: 'TSalaryDetail',
          component: TSalaryDetail,
          meta: {
            requireAuth: true,
            title: '临时工资详情',
            type:'管理员',
            header:'临时工资详情',
          }
        },
        {
          path: '/salaryManageDetail',
          name: 'SalaryManageDetail',
          component: SalaryManageDetail,
          meta: {
            requireAuth: true,
            title: '工资管理详情',
            type:'管理员',
            header:'工资管理详情',
          }
        },
        {
          path: '/empSalaryInfo',
          name: 'EmpSalaryInfo',
          component: EmpSalaryInfo,
          meta: {
            requireAuth: true,
            title: '员工工资管理',
            type:'管理员',
            header:'员工工资管理',
          }
        }
      ]
    },
    {
      path: '/aLogin',
      name: 'ALogin',
      component: ALogin,
      meta:{
        // 页面标题title
        title: '管理员登录'
      }
    },
    {
      path: '/eLogin',
      name: 'ELogin',
      component: ELogin,
      meta:{
        // 页面标题title
        title: '员工登录'
      }
    }
  ]
})



