<template>
  <div class="salary">
  <!-- 表格 -->
  <div class="list">
    <el-table
      :data="salaryList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
      style="width: 100%;border-color: #3a8ee6;"
      :default-sort = "{prop: 'time', order: 'descending'}"
      border
      :header-cell-style="{background:'#eef1f6',color:'#333333'}"
      @current-change="handleCurrentsChange"
    >
      <el-table-column
        prop="time"
        label="时间"
        sortable
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="regularpay"
        label="基本工资"
        sortable
        align="center">
      </el-table-column>
      <el-table-column
        prop="overtime"
        label="加班"
        sortable
        align="center">
      </el-table-column>
      <el-table-column
        prop="absence"
        label="请假"
        sortable
        align="center">
      </el-table-column>
      <el-table-column
        prop="allowanceids"
        label="补贴"
        sortable
        align="center">
      </el-table-column>
      <el-table-column
        prop="awards"
        label="奖金"
        sortable
        align="center">
      </el-table-column>
      <el-table-column
        prop="initialsum"
        label="工资合计"
        sortable
        align="center">
      </el-table-column>
      <el-table-column
        prop="insurancebase"
        label="社保基数"
        sortable
        align="center">
      </el-table-column>
      <el-table-column
        prop="secondsum"
        label="应付金额"
        sortable
        align="center">
      </el-table-column>
      <el-table-column
        prop="tax"
        label="代扣个税"
        sortable
        align="center">
      </el-table-column>
      <el-table-column
        prop="cutids"
        label="扣款"
        sortable
        align="center">
      </el-table-column>
      <el-table-column
        prop="sumpayable"
        label="实发金额"
        sortable
        align="center">
      </el-table-column>
      <el-table-column
        prop="note"
        label="备注"
        sortable
        align="center">
      </el-table-column>
      <el-table-column
        prop="operate"
        label="操作"
        sortable
        align="center">
        <template scope="scope" >
        <el-button type="primary" @click="detail(scope.row.userobjectid)" plain style="padding: 12px 15px" >查看</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <div class="page">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5, 10, 20, 40]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="salaryList.length"
      ></el-pagination>
    </div>
  </div>
  </div>
</template>
<script>

    export default {
        data() {
            return {
                salaryList: [],
                currentPage: 1, //初始页
                pagesize: 10,
                flag: false,
            };
        },
        components: {

        },
        computed: {
            eid() {
                return this.$store.state.employer.id;
            }
        },
        created(){
            let _this = this;
            this.$axios
                .get("/getSalaryByEid?eid="+this.eid)
                .then(function(res) {
                    if (res.data) {
                        _this.salaryList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
        },
        methods: {
            detail(userobjectid) {
                //指定跳转地址
                this.$router.push({
                    path: "/eSalaryDetail",
                    query: { userobjectid: userobjectid}
                })
            },
            handleCurrentsChange(val) {
                this.currentRow = val;

                this.changeDept = val;
                console.log(this.changeDept)
                // this.changeList = []
            },
            handleSizeChange: function(size) {
                this.pagesize = size;
                // console.log(this.pagesize)  //每页下拉显示数据
            },
            handleCurrentChange: function(currentPage) {
                this.currentPage = currentPage;
                // console.log(this.currentPage)  //点击第几页
            }
        },
    }
</script>
<style>
  .list {
    position: relative;
  }
  .page {
    /* margin-top: 20px;
      margin-left: 800px; */
    position: absolute;
    right: 0;
    bottom: -50px;
  }
  .el-table--border:after,
  .el-table--group:after,
  .el-table:before {
    background-color: #3a8ee6;
  }

  .el-table--border,
  .el-table--group {
    border-color: #3a8ee6;
  }

</style>



