<template>
  <div>
    <el-container>
      <el-header class="my-header">
        <div class="left" >
          <i class="el-icon-s-home" style="font-size:25px;"></i>
          <span style="font-size:20px;">艾默工资管理系统软件V3.0</span>
        </div>

        <!-- 全屏 -->
        <div class="btn-fullscreen" @click="handleFullScreen">
          <el-tooltip effect="dark" :content="fullscreen ? `取消全屏`:`全屏显示`" placement="bottom" style="font-size:20px;">
            <i style="font-size:20px; margin-right:10px" class="el-icon-full-screen">  全屏</i>
          </el-tooltip>
        </div>

        <!-- 管理员 -->
        <div class="user" v-if="this.type ==='管理员'" >
            <span style="font-size:20px; margin-right:10px ;align-content: center">
              <i class="el-icon-s-custom" style="font-size:20px"></i>
              {{this.aName}}
            </span>
        </div>

        <!-- 员工 -->
        <div class="user" v-if="this.type ==='员工'" align="center">
            <span style="font-size:20px; margin-right:10px ;align-content: center">
              <i class="el-icon-s-custom" style="font-size:20px"></i>
              {{this.eName}}
            </span>
        </div>

        <div class="right" >
          <span style="font-size:20px">
              <i class="el-icon-user" style="font-size:20px"></i>
              <span @click="exit">登出</span>
            </span>
        </div>

      </el-header>
    </el-container>
  </div>
</template>

<script>
    export default {
        name: "headTop",
        data() {
            return {
                fullscreen: false,
                drawer: false,
                direction: "rtl"
            };
        },
        components: {

        },
        computed: {
            aName() {
                    return this.$store.state.accountant.name;
            },
            eName() {
                    return this.$store.state.employer.name;
            },
            type(){
                    return this.$route.meta.type;
            }
        },
        created() {},
        methods: {
            exit() {
                // this.$axios
                //     .post('/exit')
                //     . then(successResponse => {
                //         if(successResponse.data.code === 200){
                //             this.$message({
                //                 message: '登出成功！！！',
                //                 type: 'success',
                //                 center: true
                //             });
                //         }
                        //指定跳转地址
                        this.$router.push({
                            path: "/login"
                        })
                        this.$message({
                            message: '登出成功！！！',
                            type: 'success',
                            center: true
                        });
                // })

            },
            handleFullScreen() {
                let element = document.documentElement;
                if (this.fullscreen) {
                    if (document.exitFullscreen) {
                        document.exitFullscreen();
                    } else if (document.webkitCancelFullScreen) {
                        document.webkitCancelFullScreen();
                    } else if (document.mozCancelFullScreen) {
                        document.mozCancelFullScreen();
                    } else if (document.msExitFullscreen) {
                        document.msExitFullscreen();
                    }
                } else {
                    if (element.requestFullscreen) {
                        element.requestFullscreen();
                    } else if (element.webkitRequestFullScreen) {
                        element.webkitRequestFullScreen();
                    } else if (element.mozRequestFullScreen) {
                        element.mozRequestFullScreen();
                    } else if (element.msRequestFullscreen) {
                        // IE11
                        element.msRequestFullscreen();
                    }
                }
                this.fullscreen = !this.fullscreen;
            },
            handleClose(done) {
                done();
            }
        }
    };
</script>

<style>
  html,
  body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
  }
  .my-header {
    position: relative;
  }
  .el-header {
    background-color: rgb(245, 246, 246);
    line-height: 60px;
    box-shadow: 0 2px 12px 0 rgba(102, 100, 100, 0.1);
    border-bottom: 1px solid #dcdfe6;
  }
  .right {
    /* float: right; */
    position: absolute;
    top: 0;
    right: 20px;
  }
  .user{
    position: absolute;
    top: 0;
    right: 80px;
    width:180px;
  }
  .left {
    position: absolute;
    left: -10px;
    width: 300px;
    height: 30px;

  }
  .btn-fullscreen {
    position: absolute;
    right: 250px;
    top: 2px;
    margin-right: 10px;
    /* display: block; */
    /* float: right; */
  }
</style>



