<template>
  <div class="workday">
  <div class="topButton">
    <el-button type="primary" @click="showAdd()" style="padding: 12px 15px;float: left">添加</el-button>
  </div>
  <!-- 表格 -->
  <div class="list">
    <el-table
      :data="workdayList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
      style="width: 100%;border-color: #3a8ee6;"
      :default-sort = "{prop: 'num', order: 'ascending'}"
      border
      :header-cell-style="{background:'#eef1f6',color:'#333333'}"
      @current-change="handleCurrentsChange"
    >
      <el-table-column
        prop="num"
        label="序号"
        sortable
        width="100"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="name"
        label="姓名"
        sortable
        width="120"
        align="center">
      </el-table-column>
      <el-table-column
        prop="eid"
        label="员工号"
        sortable
        width="100"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="absence"
        label="请假（小时）"
        sortable
        width="150"
        align="center">
      </el-table-column>
      <el-table-column
        prop="overtimen"
        label="普通加班（小时）"
        sortable
        width="170"
        align="center">
      </el-table-column>
      <el-table-column
        prop="overtimed"
        label="假日加班（小时）"
        sortable
        width="170"
        align="center">
      </el-table-column>
      <el-table-column
        prop="absenteeism"
        label="旷工"
        sortable
        width="100"
        align="center">
      </el-table-column>
      <el-table-column
        prop="late"
        label="迟到"
        sortable
        width="100"
        align="center">
      </el-table-column>
      <el-table-column
        prop="leaveral"
        label="早退"
        sortable
        width="100"
        align="center">
      </el-table-column>
      <el-table-column
        prop="status"
        label="状态"
        sortable
        width="120"
        align="center">
        <template scope="scope">
          <el-tag
            :type="scope.row.status === '未提交' ? 'primary' : 'success'"
            disable-transitions>{{scope.row.status}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        prop="time"
        label="时间"
        sortable
        width="170"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="operate"
        label="操作"
        width="200"
        align="center"
      >
        <template scope="scope">
          <el-button type="warning" @click="showChange()" style="padding: 12px 15px">修改</el-button>
          <el-button type="danger"  @click="showDelete()" style="padding: 12px 15px">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <div class="page">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5, 10, 20, 40]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="workdayList.length"
      ></el-pagination>
    </div>
  </div>

  <!-- 添加弹框 -->
  <div v-if="flag" class="addWorkday">
    <div class="title">
      <h2>新增请假加班记录</h2>
      <i class="el-icon-close" @click="closeAdd()"></i>
    </div>

    <div class="add">

      <el-form ref="form" :model="addList" label-width="80px">
        <el-form-item label="员工姓名">
          <el-select v-model="addList.name" placeholder="请选择员工姓名">
            <el-option  v-for="item in employerList"
                        :label="item.name"
                        :value="item.name">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="请假">
          <el-input v-model="addList.absence" prop="absence"></el-input>
        </el-form-item>
        <el-form-item label="普通加班">
          <el-input v-model="addList.overtimen"></el-input>
        </el-form-item>
        <el-form-item label="假日加班">
          <el-input v-model="addList.overtimed"></el-input>
        </el-form-item>
        <el-form-item label="旷工">
          <el-input v-model="addList.absenteeism"></el-input>
        </el-form-item>
        <el-form-item label="迟到">
          <el-input v-model="addList.late"></el-input>
        </el-form-item>
        <el-form-item label="早退">
          <el-input v-model="addList.leaveral"></el-input>
        </el-form-item>
        <el-form-item label="时间">
          <el-select v-model="addList.time" placeholder="请选择时间">
            <el-option  v-for="item in createdateList"
                        :label="item.name"
                        :value="item.name">
            </el-option>
          </el-select>

        </el-form-item>

        <div class="addButton">
          <el-form-item>
            <el-button type="primary" @click="addWorkday()" style="padding: 12px 15px">添加</el-button>
            <el-button @click="addRest()" style="padding: 12px 15px">重置</el-button>
          </el-form-item>
        </div>
      </el-form>
    </div>
  </div>

  <!-- 修改弹框 -->
  <div v-if="flag2" class="changeWorkday">
    <div class="title">
      <h2>修改请假加班管理</h2>
      <i class="el-icon-close" @click="closeChange()"></i>
    </div>

    <div class="change">
      <el-form ref="form" :model="changeList" label-width="80px">
        <el-form-item label="员工姓名">
          <el-input v-model="changeList.name" readonly="true"></el-input>
        </el-form-item>
        <el-form-item label="请假">
          <el-input v-model="changeList.absence"></el-input>
        </el-form-item>
        <el-form-item label="普通加班">
          <el-input v-model="changeList.overtimen"></el-input>
        </el-form-item>
        <el-form-item label="假日加班">
          <el-input v-model="changeList.overtimed"></el-input>
        </el-form-item>
        <el-form-item label="旷工">
          <el-input v-model="changeList.absenteeism"></el-input>
        </el-form-item>
        <el-form-item label="迟到">
          <el-input v-model="changeList.late"></el-input>
        </el-form-item>
        <el-form-item label="早退">
          <el-input v-model="changeList.leaveral"></el-input>
        </el-form-item>
        <el-form-item label="时间">
          <el-select v-model="addList.time" placeholder="请选择时间">
            <el-option  v-for="item in createdateList"
                        :label="item.name"
                        :value="item.name">
            </el-option>
          </el-select>
        </el-form-item>
        <div class="saveButton">
          <el-form-item>
            <el-button type="primary" @click="changeWorkday()" style="padding: 12px 15px">修改</el-button>
          </el-form-item>
        </div>
      </el-form>
    </div>
  </div>

  <!-- 删除弹框 -->
  <div class="delete" v-if="flag3">
    <div class="title">
      <span>删除提示</span>
      <i class="el-icon-close" @click="closeDelete()"></i>
    </div>
    <div class="main">
      <i class="el-icon-warning"></i>
      <span>您确定要删除此条数据吗？</span>
    </div>
    <div class="deleteButton">
      <el-button  type="danger" @click="deleteInfo()" style="padding: 12px 15px">确定</el-button>
      <el-button  @click="closeDelete()" style="padding: 12px 15px">取消</el-button>
    </div>
  </div>

    <div class="popContainer" v-if="flag"></div>
    <div class="popContainer" v-if="flag2"></div>
    <div class="popContainer" v-if="flag3"></div>

  </div>
</template>

<script>
    export default {
        data() {
            return {
                workdayList: [],
                flag: false,
                flag2: false,
                flag3: false,
                currentPage: 1, //初始页
                pagesize: 10,
                flag: false,
                addList: [
                    {
                        id:"",
                        name:"",
                        absence: "",
                        overtimen: "",
                        overtimed: "",
                        absenteeism: "",
                        late: "",
                        leaveral:"",
                        time:""
                    }
                ],
                employerList:[

                ],
                createdateList:[

                ],
                changeList: [
                    {
                        id:"",
                        name:"",
                        absence: "",
                        overtimen: "",
                        overtimed: "",
                        absenteeism: "",
                        late: "",
                        leaveral:"",
                        time:""
                    }
                ],
                deleteList: [
                    {
                        id:"",
                        name:"",
                        time:"",
                        absence: "",
                        overtimen: "",
                        overtimed: "",
                        absenteeism: "",
                        late: "",
                        leaveral:""
                    }
                ],

            };
        },
        created(){
            let _this = this;
            this.$axios
                .get("/getAllWorkday")
                .then(function(res) {
                    if (res.data) {
                        _this.workdayList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
            this.$axios
                .get("/getAllEmployer")
                .then(function(res) {
                    if (res.data) {
                        _this.employerList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
            this.$axios
                .get("/getAllCreatedate")
                .then(function(res) {
                    if (res.data) {
                        _this.createdateList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
        },
        inject: ["reload"],
        methods: {
            addWorkday() {
                let _this = this
                    this.$axios
                        .post('/addWorkday', {
                            name: this.addList.name,
                            time: this.addList.time,
                            absence: this.addList.absence,
                            overtimen: this.addList.overtimen,
                            overtimed: this.addList.overtimed,
                            absenteeism: this.addList.absenteeism,
                            late: this.addList.late,
                            leaveral: this.addList.leaveral
                        })
                        .then(successResponse  => {
                            if (successResponse.data.code === 200) {
                                this.flag = !this.flag;
                                this.reload();
                                this.$message({
                                    message: '添加成功！！！',
                                    type: 'success',
                                    center: true
                                });
                            }else {
                                this.reload();
                                this.$message.error('添加失败！');
                            }
                        })
                        .catch(failResponse => {
                            this.reload();
                            this.$message.error('添加信息填写不正确！');
                        });
            },

            changeWorkday() {
                let _this = this
                this.$axios
                    .put('/updateWorkday', {
                        id: this.changeList.id,
                        time: this.changeList.time,
                        absence: this.changeList.absence,
                        overtimen: this.changeList.overtimen,
                        overtimed: this.changeList.overtimed,
                        absenteeism: this.changeList.absenteeism,
                        late: this.changeList.late,
                        leaveral: this.changeList.leaveral
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            this.flag2 = !this.flag2;
                            this.reload();
                            this.$message({
                                message: '修改成功！！！',
                                type: 'success',
                                center: true
                            });
                        }else {
                            this.reload();
                            this.$message.error('修改失败！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('修改后信息填写不正确！');
                    });
            },
            deleteInfo() {
                let _this = this
                this.$axios
                    .delete('/deleteWorkday', {
                        data: {
                            id:this.deleteList.id
                        }
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            this.flag3 = !this.flag3;
                            this.reload();
                            this.$message({
                                message: '删除成功！！！',
                                type: 'success',
                                center: true
                            });
                        }else {
                            this.reload();
                            this.$message.error('删除失败！');
                        }
                    })
                    .catch(failResponse => {
                          this.reload();
                          this.$message.error('删除失败！');
                    });
            },

            showAdd() {
                this.flag = !this.flag;
                this.addList = [];
            },

            showChange() {

                    this.flag2 = !this.flag2;
            },
            showDelete() {

                    this.flag3 = !this.flag3;
            },

            closeAdd() {
                this.flag = !this.flag;
                this.$message("操作已取消");
                this.reload();
            },


            closeChange() {
                this.flag2 = !this.flag2;
                this.$message("操作已取消");
                this.reload();
            },
            closeDelete() {
                this.flag3 = !this.flag3;
                this.$message("操作已取消");
                this.reload();
            },

            addRest() {
                this.addList = [];
            },

            handleCurrentsChange(val) {
                this.currentRow = val;
                this.addList = val;
                this.changeList = val;
                this.deleteList = val;
            },
            handleSizeChange: function(size) {
                this.pagesize = size;
                // console.log(this.pagesize)  //每页下拉显示数据
            },
            handleCurrentChange: function(currentPage) {
                this.currentPage = currentPage;
                // console.log(this.currentPage)  //点击第几页
            }
        },

    }
</script>
<style>
  .topButton {
    width: 100%;
    height: 50px;
    line-height: 50px;
    padding-bottom: 10px;
  }
  .el-table-column {
    text-align: center;
  }
  .el-submenu .el-menu-item:hover {
    background-color: rgb(232, 243, 255);
  }
  .list {
    position: relative;
  }
  .page {
    position: absolute;
    right: 0;
    bottom: -50px;
  }

  .el-table--border:after,
  .el-table--group:after,
  .el-table:before {
    background-color: #3a8ee6;
  }

  .el-table--border,
  .el-table--group {
    border-color: #3a8ee6;
  }

  .addWorkday {
    position: absolute;
    z-index: 1001;
    width: 400px;
    height: 680px;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  .addWorkday .title {
    height: 50px;
    width: 100%;
    text-align: center;
    line-height: 30px;
    color: #409eff;
    text-shadow: 2px 2px 2px #ccc;
  }
  .addWorkday .title i {
    position: relative;
    top: -55px;
    right: -170px;
    color: black;
    font-size: 20px;
  }
  .addWorkday .el-form-item__label{
    color: black;
    font-weight: bold;
  }
  .addWorkday .add {
    margin-right: 20px;
    margin-top: 30px;
  }
  .addWorkday .addButton {
    position: relative;
    margin-left:20px;
    margin-top: 30px;
  }
  .addWorkday .addButton .el-button {
    width: 80px;
  }
  .addWorkday .el-select {
    width: 280px;
  }
  .changeWorkday {
    position: absolute;
    z-index: 1001;
    width: 400px;
    height: 680px;
    /* border:1px solid #dcdfe6; */
    left: 0;
    top: 0px;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  .changeWorkday .title {
    height: 50px;
    width: 100%;
    text-align: center;
    line-height: 30px;
    /* border-bottom: 1px solid #dcdfe6; */
    color: #409eff;
    text-shadow: 2px 2px 2px #ccc;
  }
  .changeWorkday .title i {
    position: relative;
    top: -55px;
    right: -170px;
    color: black;
    font-size: 20px;
  }
  .changeWorkday .el-form-item__label{
    color: black;
    font-weight: bold;
  }
  .changeWorkday .change {
    margin-right: 20px;
    margin-top: 30px;
  }

  .changeWorkday .saveButton {
    margin-left:-20px;
    margin-top: 30px;
  }

  .changeWorkday .saveButton .el-button {
    width: 80px;
  }
  .changeWorkday .el-select {
    width: 280px;
  }
  .delete {
    position: absolute;
    z-index: 1001;
    width: 400px;
    height: 180px;
    border: 1px solid #dcdfe6;
    left: 0;
    top: -50px;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
  }
  .title {
    width: 100%;
    height: 45px;
    line-height: 45px;
  }
  .title span {
    float: left;
    margin-left: 20px;
  }
  .delete .title i {
    float: right;
    margin-top: 10px;
    margin-right: 10px;
  }
  .delete .main {
    height: 60px;
    width: 100%;
    line-height: 50px;
    margin-top: 15px;
    margin-left: 0px;
  }
  .delete .main i {
    color: #e6a23c;
    font-size: 20px;
    margin-right: 10px;
  }
  .delete .main span {
    margin-bottom: 5px;
  }
  .delete .deleteButton {
    float: right;
    margin-right: 20px;
    margin-top: 5px;
  }
  .popContainer {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 500;
  }
</style>



