<template>
  <body id = "poster">
    <el-form class="login-container" label-position="left"
                       label-width="0px">
    <h3 class="login_title">管理员登录</h3>
    <el-form-item>
      <el-input type="text" v-model="loginForm.name"
                auto-complete="off" id="name" placeholder="账号"></el-input>
    </el-form-item>
    <el-form-item>
      <el-input type="password" v-model="loginForm.password"
                auto-complete="off" id="password" placeholder="密码"></el-input>
    </el-form-item>
      <p id="error" style="color:red;text-align:center;height: 10px"></p>
    <el-form-item style="width: 100%">
      <el-button type="primary" style="width: 30%;background: #505458;border: none" v-on:click="login">登录</el-button>
      <el-button type="primary" style="width:30%;background: #505458;border: none" v-on:click="exit">返回</el-button>
    </el-form-item>
  </el-form>
  </body>
</template>

<script>

  export default {
    name: 'aLogin',
    data () {
      return {
        loginForm: {
          name: '',
          password: ''
        },
        responseResult: []
      }
    },
    methods: {
        exit() {
            //指定跳转地址
            this.$router.replace({path: '/login'});
          },
      login () {
          this.name = this.loginForm.name;
          this.password = this.loginForm.password;
          if(this.name.length === 0){
              this.$message({
                  message: '账号不能为空！',
                  type: 'warning'
              });
          } else if (this.password.length < 6) {
              this.$message({
                  message: '密码的长度不能小于6位！',
                  type: 'warning'
              });
          } else {
              var _this = this
          console.log(this.$store.state)
          this.$axios
              .post('/aLogin', {
                  name: this.loginForm.name,
                  password: this.loginForm.password
              })
              .then(successResponse => {
                  if (successResponse.data.code === 200) {
                      _this.$store.commit('aLogin', _this.loginForm);
                      var path = this.$route.query.redirect;
                      this.$router.replace({path: path === '/' || path === undefined ? '/aIndex' : path});
                      this.$message({
                          message: '登陆成功！！！',
                          type: 'success',
                          center: true
                      });
                  } else {
                      this.$message.error('登陆失败，用户名或者密码错误！');
                  }
              })
              .catch(failResponse => {

              })
      }
      }
    }
  }
</script>
<style>
  .login-container {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 90px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }

  .login_title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }

  #poster {
    background:url("../assets/background.jpg") no-repeat;
    background-position: top;
    height: 100%;
    width: 100%;
    background-size: cover;
    position: fixed;
  }
  body{
    margin: 0px;
  }

</style>



