<template>
  <body id = "poster">
  <h1 style=" text-align:left;color:white;text-shadow:1px 1px 0 black;font-size:55px;font-weight:normal;position: relative;top:32%;left:9%;">艾默工资管理系统软件V3.0</h1>
  <p style="text-align:left;margin-left: 7px;color: white;font-size:25px;font-weight:200;position: relative;top:32%;left:9%;">
    让您的工作变得简单高效。使用前请登录：
  </p>
  <el-button id = "emp" type="text" @click="open1">员工登录</el-button>
  <el-button id = "admin" type="text" @click="open2">管理员登录</el-button>
  <p align="center" style="position: absolute;bottom: 0;left:810px;margin:10px;text-align: center;font-size: 15px;color: #969a9c;font-weight: bold" >Copyright © 扬州艾默软件技术有限公司</p>
  </body>
</template>

<script>
    export default {
        methods: {
            open1() {
                //指定跳转地址
                this.$router.replace('/eLogin')
            },
            open2() {
                //指定跳转地址
                this.$router.replace('/aLogin')
            }
        }
    }
</script>
<style>

  #poster {
    background:url("../assets/background.jpg") no-repeat;
    background-position: top;
    height: 100%;
    width: 100%;
    background-size: cover;
    position: fixed;
  }
  body{
    margin: 0px;
  }

  #emp {
     background-color: white;
     height: 40px;
     width: 100px;
     position: absolute;
     top:55%;
     left:9%;
     color: gray;
     text-size:50px;
   }

  #admin {
    background-color: white;
    height: 40px;
    width: 100px;
    position: absolute;
    top:55%;
    left:16%;
    color: gray;
    text-size:50px;
  }

</style>



