<template>
  <div class="tempo">
    <div class="topButton">
      <el-button type="primary" @click="print()" style="padding: 12px 15px;float: left">打印工资单</el-button>
    </div>
    <!-- 表格 -->
    <div class="list">
      <el-table
        id ="tempoTable"
        :data="tempoList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        style="width: 100%;border-color:#3a8ee6;"
        border
        :header-cell-style="{background:'#eef1f6',color:'#333333'}"
        @current-change="handleCurrentsChange"
      >
        <el-table-column
          align="center"
          cell-class-name="tableHead"
          :render-header="renderFirstHeader"
        >
        <el-table-column
          align="center"
          :render-header="renderSecondHeader"
        >
        <el-table-column
          prop="name"
          label="姓名"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="secondsum"
          label="工资合计"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="tax"
          label="代扣个税"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="sumpayable"
          label="实发金额"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="sign"
          label="签章"
          align="center">
        </el-table-column>
        <el-table-column
          prop="note"
          label="备注"
          align="center">
        </el-table-column>
        </el-table-column>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 40]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="tempoList.length"
        ></el-pagination>
      </div>
    </div>
  </div>

</template>

<script>
    export default {
        data() {
            return {
                time:this.$route.query.time,
                tempoList: [],
                currentPage: 1, //初始页
                pagesize: 10,
            };
        },

        created(){
            let _this = this;
            let time = this.$route.query.time;
            this.$axios
                .get("/getTempoByCdid?time="+this.time)
                .then(function(res) {
                    if (res.data) {
                        _this.tempoList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
        },
      methods:{
          //表头显示表名
          renderFirstHeader(h) {
              //下拉框内容包裹在一个div里面
              return h('div',{
              },[
                  h("span",{
                      style: {
                          fontSize:'18px'
                      },
                      class:'level-font-class'
                  },"扬州艾默软件工资发放表"),
              ])
          },
          //表头显示日期
          renderSecondHeader(h) {
              //下拉框内容包裹在一个div里面
              return h('div',{
              },[
                  h("span",{
                      style: {
                          fontSize:'15px'
                      },
                      class:'level-font-class'
                  },this.time),
              ])
          },
          //打印页面内容
          print(){
              var print= document.getElementById('tempoTable');
              var newContent = print.innerHTML;
              var oldContent = document.body.innerHTML;
              document.body.innerHTML = newContent;
              document.getElementsByTagName('body')[0].style.zoom=0.92;
              document.getElementsByTagName('body')[0].style.zoom=0.78;
              window.print();
              window.location.reload();
              //将原有页面还原到页面
              document.body.innerHTML = oldContent;
              return false;
          },
          handleCurrentsChange(val) {
              this.currentRow = val;
          },
          handleSizeChange: function(size) {
              this.pagesize = size;
              // console.log(this.pagesize)  //每页下拉显示数据
          },
          handleCurrentChange: function(currentPage) {
              this.currentPage = currentPage;
              // console.log(this.currentPage)  //点击第几页
          }
      }

    }
    console.log(this.time);

</script>

<style>
  #tempoTable td,.building-top .el-table th.is-leaf {
    border-bottom:  1px solid #ccc;
    border-left: 1px solid #ccc;
    border-top: 1px solid #ccc;
    border-right: 1px solid #ccc;
  }
  #tempoTable th,.building-top .el-table th.is-leaf {
    border-bottom:  1px solid #ccc;

  }

  .tableHead{
    font-size: 20px;
    height: 30px;
  }
  .topButton {
    width: 100%;
    height: 50px;
    line-height: 50px;
    padding-bottom: 10px;
  }
  .el-submenu .el-menu-item:hover {
    background-color: rgb(232, 243, 255);
  }
  .list {
    position: relative;
  }
  .page {
    position: absolute;
    right: 0;
    bottom: -50px;
  }

  .el-table--border:after,
  .el-table--group:after,
  .el-table:before {
    background-color: #3a8ee6;
  }

  .el-table--border,
  .el-table--group {
    border-color: #3a8ee6;
  }

  @page{
    size: A4 landscape; /*portrait： 纵向打印,  landscape: 横向*/
  }

  @media print {
    .el-table thead.is-group th {
      text-align: center
    }
    tbody {
      text-align: center;
      border: 1px solid #000;
    }
    th {
      border: 1px solid #000;
    }
    td {
      border: 1px solid #000;
    }
    table,
    tbody,
    thead {
      width: 100% !important;
    }
    colgroup {
      position: absolute;
      width: 100% !important;
    }
  }

</style>



