<template>
  <div class="userdefault1">
    <div class="topButton">
      <el-button type="primary" @click="showAdd()" style="padding: 12px 15px;float: left">添加</el-button>
      <el-button type="primary" @click="back()" style="padding: 12px 15px;float: left">返回</el-button>
    </div>
    <!-- 表格 -->
    <div class="list">
      <el-table
        :data="userdefault1List.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        style="width: 100%;border-color:#3a8ee6"
        :default-sort = "{prop: 'num', order: 'ascending'}"
        border
        :header-cell-style="{background:'#eef1f6',color:'#333333'}"
        @current-change="handleCurrentsChange"
      >
        <el-table-column
          prop="eid"
          label="员工工号"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="ename"
          label="姓名"
          align="center">
        </el-table-column>
        <el-table-column
          prop="regularpay"
          label="基本工资"
          align="center">
        </el-table-column>
        <el-table-column
          prop="allowanceids"
          label="补贴"
          align="center">
        </el-table-column>
        <el-table-column
          prop="cutids"
          label="扣款"
          align="center">
        </el-table-column>
        <el-table-column
          prop="insurancebase"
          label="社保基数"
          align="center">
        </el-table-column>
        <el-table-column
          prop="updatedate"
          label="更新时间"
          align="center">
        </el-table-column>
        <el-table-column
          prop="note"
          label="备注"
          align="center">
        </el-table-column>
        <el-table-column
          prop="operate"
          label="操作"
          align="center">
          <template scope="scope" >
            <el-button type="danger"  @click="showDelete()" style="padding: 12px 15px">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 40]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="userdefault1List.length"
        ></el-pagination>
      </div>
    </div>

    <!-- 添加弹框 -->
    <div v-if="flag" class="addUserdefault1" ref="addUserdefault1" :style="addUserdefault1Style">
      <div class="title">
        <h2>新增员工工资信息</h2>
        <i class="el-icon-close" @click="closeAdd()"></i>
      </div>

      <div class="add">
        <el-form ref="form" :model="addList" label-width="80px">
          <el-form-item label="员工姓名" style="position: relative;left: 90px;top:-25px">
              <span v-model="addList.ename" style="position: relative;left:-255px;width:150px">{{this.employerList[0].name}}</span>
          </el-form-item>
          <el-form-item label="社保基数" style="position: relative;left: 440px;top:-85px ">
            <el-input v-model="addList.insurancebase" style="position: relative;left:-255px;width:150px"></el-input>
          </el-form-item>
          <el-form-item label="基本工资" style="position: relative;left: 90px;top:-85px">
            <el-input v-model="addList.regularpay" style="position: relative;left:-255px;width:150px"></el-input>
          </el-form-item>
          <el-form-item label="更新时间" style="position: relative;left: 440px;top:-145px">
            <el-date-picker
              v-model="addList.updatedate"
              type="date"
              placeholder="选择日期"
              style="width:150px;position:relative;left:-255px"
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item label="备注" style="position: relative;left: 70px;top:-145px">
            <el-input v-model="addList.note" style="position: relative;left:-236px;width:150px"></el-input>
          </el-form-item>

          <span style="float: left;position: relative;left:97px;top:-135px;font-weight: bold;color: black">每月补贴</span>
          <div class="topButton">
            <el-button type="primary" @click="AddForm()" style="padding: 12px 15px;position: relative;left:-160px;top:-150px">增加补贴</el-button>
          </div>
          <span style="font-weight: bold;color:black;font-size: 15px;margin: 50px;position:relative;top:-155px;left:-95px">补贴种类</span>
          <span style="font-weight: bold;color:black;font-size: 15px;position:relative;left:10px;top:-155px">补贴数目</span >
          <el-form :inline="true" class="demo-form-inline" v-for="(item, i) in FormArr" :key="i">

            <el-select v-model="item.category" placeholder="请选择补贴种类" style="width: 150px;margin: 5px;top:-155px;left:-40px">
              <el-option  v-for="item in allowancecategoryList"
                          :label="item.name"
                          :value="item.name">
              </el-option>
            </el-select>
            <el-input v-model="item.allowance" style="width: 150px;margin-right: 5px;top:-155px;left:20px"></el-input>
            <i class="el-icon-delete-solid" @click="Delete(item.index)" style="position:relative;top:-155px;left:50px"></i>
               
          </el-form>


          <span style="float: left;position: relative;left:97px;top:-130px;font-weight: bold;color: black">每月扣款</span>
          <div class="topButton">
            <el-button type="primary" @click="AddForm1()" style="padding: 12px 15px;position: relative;left:-160px;top:-140px">增加扣款</el-button>
          </div>
          <span style="font-weight: bold;color:black;font-size: 15px;margin: 50px;position:relative;top:-150px;left:-95px">扣款种类</span>
          <span style="font-weight: bold;color:black;font-size: 15px;position:relative;left:10px;top:-150px">扣款数目</span >
          <el-form :inline="true" class="demo-form-inline" v-for="(item, i) in FormArr1" :key="i">

            <el-select v-model="item.category" placeholder="请选择扣款种类" style="width: 150px;margin: 5px;top:-150px;left:-40px">
              <el-option  v-for="item in cutcategoryList"
                          :label="item.name"
                          :value="item.name">
              </el-option>
            </el-select>
            <el-input v-model="item.cut" style="width: 150px;margin-right: 5px;top:-150px;left:20px"></el-input>
            <i class="el-icon-delete-solid" @click="Delete1(item.index)" style="position:relative;top:-150px;left:50px"></i>
               
          </el-form>



          <div class="addButton">
            <el-form-item>
              <el-button type="primary" @click="addUserdefault1()" style="padding: 12px 15px">添加</el-button>
              <el-button @click="addRest()" style="padding: 12px 15px">重置</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>

    <!-- 删除弹框 -->
    <div class="delete" v-if="flag3">
      <div class="title">
        <span>删除提示</span>
        <i class="el-icon-close" @click="closeDelete()"></i>
      </div>
      <div class="main">
        <i class="el-icon-warning"></i>
        <span>您确定要删除此员工工资信息吗？</span>
      </div>
      <div class="deleteButton">
        <el-button  type="danger" @click="deleteInfo()" style="padding: 12px 15px">确定</el-button>
        <el-button  @click="closeDelete()" style="padding: 12px 15px">取消</el-button>
      </div>
    </div>

    <div class="popContainer" v-if="flag"></div>
    <div class="popContainer" v-if="flag3"></div>

  </div>
</template>

<script>
    export default {
        data() {
            return {
                userdefault1List: [],
                id:this.$route.query.id,
                flag: false,
                flag3: false,
                currentPage: 1, //初始页
                pagesize: 10,
                flag: false,
                addUserdefault1Style:{
                    height:'630px'
                },

                employerList:[

                ],
                cutcategoryList:[

                ],
                allowancecategoryList:[

                ],
                addList: [
                    {
                        eid:"",
                        updatedate:"",
                        ename:"",
                        regularpay:"",
                        allowanceids: "",
                        cutids:"",
                        insurancebase:"",
                        note:"",
                    }
                ],
                FormArr: [
                    {
                        index: 0,
                        value: ''
                    }
                ],
                FormArr1: [
                    {
                        index: 0,
                        value: ''
                    }
                ],
                deleteList: [
                    {
                        id:""
                    }
                ],

            };
        },
        mounted(){

        },
        created(){
            let _this = this;
            this.$axios
                .get("/getUserdefault1ByEid?eid="+this.$route.query.id)
                .then(function(res) {
                    if (res.data) {
                        _this.userdefault1List = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
            this.$axios
                .get("/getEmployerById?id="+this.$route.query.id)
                .then(function(res) {
                    if (res.data) {
                        _this.employerList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
            this.$axios
                .get("/getAllAllowancecategory")
                .then(function(res) {
                    if (res.data) {
                        _this.allowancecategoryList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
            this.$axios
                .get("/getAllCutcategory")
                .then(function(res) {
                    if (res.data) {
                        _this.cutcategoryList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
        },
        inject: ["reload"],
        methods: {
            addUserdefault1() {
                let _this = this
                    let date =new Date(this.addList.updatedate)
                    this.updatedate =date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate();
                    this.$axios
                        .post('/addUserdefault1', {
                            ename: this.employerList[0].name,
                            insurancebase: this.addList.insurancebase,
                            regularpay: this.addList.regularpay,
                            updatedate: this.updatedate,
                            note: this.addList.note,
                        })
                        .then(successResponse => {
                            if (successResponse.data.code === 200) {
                                  for(let i =0;i<this.FormArr.length;i++){
                                      this.$axios
                                          .post('/addAllowance', {
                                              eid: this.$route.query.id,
                                              category: this.FormArr[i].category,
                                              allowance: this.FormArr[i].allowance,
                                          })
                                          .then(successResponse => {
                                              if (successResponse.data.code === 200) {
                                                  // this.$message({
                                                  //     message: '添加补贴成功！！！',
                                                  //     type: 'success',
                                                  //     center: true
                                                  // });
                                                } else  {
                                                  this.reload();
                                                  this.$message.error('添加补贴失败！');
                                              }
                                          })
                                          .catch(failResponse => {
                                              this.reload();
                                              this.$message.error('添加补贴失败！');
                                          });
                                  }
                                for(let i =0;i<this.FormArr1.length;i++){
                                    this.$axios
                                        .post('/addCut', {
                                            eid: this.$route.query.id,
                                            category: this.FormArr1[i].category,
                                            cut: this.FormArr1[i].cut,
                                        })
                                        .then(successResponse => {
                                            if (successResponse.data.code === 200) {
                                                // this.$message({
                                                //     message: '添加扣款成功！！！',
                                                //     type: 'success',
                                                //     center: true
                                                // });
                                            } else  {
                                                this.reload();
                                                this.$message.error('添加扣款失败！');
                                            }
                                        })
                                        .catch(failResponse => {
                                            this.reload();
                                            this.$message.error('添加扣款失败！');
                                        });
                                }
                                let k =0;
                                if(this.FormArr.length<=this.FormArr1.length){
                                    k = this.FormArr1.length;
                                }else{
                                    k = this.FormArr.length;
                                }
                                for(let i =0;i<k;i++) {
                                    this.$axios
                                        .put('/updateUserdefault1', {
                                            eid: this.$route.query.id
                                        })
                                        .then(successResponse => {
                                            if (successResponse.data.code === 200) {
                                                this.flag = !this.flag;
                                                this.reload();
                                                this.$message({
                                                    message: '添加员工工资信息成功！！！',
                                                    type: 'success',
                                                    center: true
                                                });
                                            } else {
                                                this.reload();
                                                this.$message.error('添加员工工资信息失败！');
                                            }
                                        })
                                        .catch(failResponse => {
                                            this.reload();
                                            this.$message.error('添加员工工资信息失败！');
                                        });
                                }
                                  } else  {
                              this.reload();
                              this.$message.error('添加失败！');
                          }
                        })
                        .catch(failResponse => {
                            this.reload();
                            this.$message.error('添加信息填写不正确！');
                        });
            },

            deleteInfo() {
                let _this = this
                this.$axios
                    .put('/deleteUserdefault1', {
                        id:this.deleteList.id
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            this.flag3 = !this.flag3;
                            this.reload();
                            this.$message({
                                message: '删除成功！！！',
                                type: 'success',
                                center: true
                            });
                        }else {
                            this.reload();
                            this.$message.error('删除失败！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('删除失败！');
                    });
            },

            back() {
                //指定跳转地址
                this.$router.push({
                    path: "/empInfoManagement",
                })
            },

            showAdd() {
                if(this.userdefault1List.length==0){
                  this.flag = !this.flag;
                  this.addList = [
                      {
                          ename:this.userdefault1List.ename,
                      }
                  ];
                }else{
                    this.$message.error('该员工工资信息已存在！');
                }
            },

            showDelete() {
                this.flag3 = !this.flag3;
            },

            closeAdd() {
                this.flag = !this.flag;
                this.$message("操作已取消");
                this.reload();
            },

            AddForm () {
                this.FormArr.push({
                    index: this.FormArr.length,
                    value: ''
                })
                let height= this.$refs.addUserdefault1.offsetHeight;
                this.addUserdefault1Style.height =(height +50)+'px';
                console.log(this.FormArr)
            },

            AddForm1 () {
                this.FormArr1.push({
                    index: this.FormArr1.length,
                    value: ''
                })
                let height= this.$refs.addUserdefault1.offsetHeight;
                this.addUserdefault1Style.height =(height +50)+'px';
                console.log(this.FormArr1)
            },

            Delete (index) {
                this.FormArr.splice(index, 1)
                for (let i in this.FormArr) {
                    this.FormArr[i].index = i
                }
                let height= this.$refs.addUserdefault1.offsetHeight;
                this.addUserdefault1Style.height =(height -50)+'px';
            },

            Delete1 (index) {
                this.FormArr1.splice(index, 1)
                for (let i in this.FormArr1) {
                    this.FormArr1[i].index = i
                }
                let height= this.$refs.addUserdefault1.offsetHeight;
                this.addUserdefault1Style.height =(height -50)+'px';
            },

            addRest() {
                this.addList = [];
                this.FormArr = [
                    {
                        index: 0,
                        value: ''
                    }
                ];
            },

            closeDelete() {
                this.flag3 = !this.flag3;
                this.$message("操作已取消");
                this.reload();
            },

            handleCurrentsChange(val) {
                this.currentRow = val;
                this.addList = val;
                this.deleteList = val;
            },
            handleSizeChange: function(size) {
                this.pagesize = size;
                // console.log(this.pagesize)  //每页下拉显示数据
            },
            handleCurrentChange: function(currentPage) {
                this.currentPage = currentPage;
                // console.log(this.currentPage)  //点击第几页
            }
        },

    }
</script>
<style>
  .topButton {
    width: 100%;
    height: 50px;
    line-height: 50px;
    padding-bottom: 10px;
  }
  .el-submenu .el-menu-item:hover {
    background-color: rgb(232, 243, 255);
  }
  .list {
    position: relative;
  }
  .page {
    position: absolute;
    right: 0;
    bottom: -50px;
  }

  .el-table--border:after,
  .el-table--group:after,
  .el-table:before {
    background-color: #3a8ee6;
  }

  .el-table--border,
  .el-table--group {
    border-color: #3a8ee6;
  }

  .addUserdefault1 {
    position: absolute;
    z-index: 1001;
    width: 800px;
    /*height: 600px;*/
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  .addUserdefault1 .title {
    height: 50px;
    width: 100%;
    text-align: center;
    line-height: 30px;
    /* border-bottom: 1px solid #dcdfe6; */
    color: #409eff;
    text-shadow: 2px 2px 2px #ccc;
  }
  .addUserdefault1 .title i {
    position: relative;
    top: -55px;
    right: -370px;
    color: black;
    font-size: 20px;
  }
  .addUserdefault1 .el-form-item__label {
    color: black;
    font-weight: bold;
  }
  .addUserdefault1 .add {
    margin-right: 30px;
    margin-top: 30px;
  }
  .addUserdefault1 .addButton {
    margin-left:-15px;
    margin-top: -120px;
  }
  .addUserdefault1 .addButton .el-button {
    width: 80px;
  }
  .addUserdefault1 .el-select {
    width: 280px;
  }

  .delete {
    position: absolute;
    z-index: 1001;
    width: 400px;
    height: 180px;
    border: 1px solid #dcdfe6;
    left: 0;
    top: -50px;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
  }
  .title {
    width: 100%;
    height: 45px;
    line-height: 45px;
  }
  .title span {
    float: left;
    margin-left: 20px;
  }
  .delete .title i {
    float: right;
    margin-top: 10px;
    margin-right: 10px;
  }
  .delete .main {
    height: 60px;
    width: 100%;
    line-height: 50px;
    margin-top: 15px;
    margin-left: 0px;
  }
  .delete .main i {
    color: #e6a23c;
    font-size: 20px;
    margin-right: 10px;
  }
  .delete .main span {
    margin-bottom: 5px;
  }
  .delete .deleteButton {
    float: right;
    margin-right: 20px;
    margin-top: 5px;
  }
  .popContainer {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 500;
  }
</style>




