<template>
      <div>

         <el-breadcrumb  class="breadcrumb-container" separator-class="el-icon-arrow-right">
            <el-breadcrumb-item  style="font-size:22px;">
              <span v-if="this.showName==='YES'" style="color: #333333;font-size:22px;">{{this.header}} / {{this.name}}<i class="el-icon-arrow-right"></i><br/><br/><br/>
              </span>
              <span v-else-if="this.header!=null" style="color: #333333;font-size:22px;">{{this.header}}<i class="el-icon-arrow-right"></i><br/><br/>
              </span>
            </el-breadcrumb-item>
        </el-breadcrumb>

      </div>
</template>

<script>
export default {
    data (){
         return{
             levelList: null
         }
    },
    computed: {
        name() {
            return this.$store.state.employer.name;
        },
        showName(){
            return this.$route.meta.showName;
        },
        header(){
            return this.$route.meta.header;
        }
    },
     created(){
    },
    methods:{

    }
}
</script>

<style>

</style>



