<template>
  <div class="taxrate">
    <div class="topButton">
      <el-button type="primary" @click="showEdit()" style="padding: 12px 15px;float: left">编辑</el-button>
      <el-button type="danger" @click="showDelete()" style="padding: 12px 15px;float: left">删除</el-button>
    </div>
    <!-- 表格 -->
    <div class="list">
      <el-table
        :data="taxrateList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        style="width: 100%;border-color:#3a8ee6"
        :default-sort = "{prop: 'num', order: 'ascending'}"
        border
        :header-cell-style="{background:'#eef1f6',color:'#333333'}"
        @current-change="handleCurrentsChange"
      >
        <el-table-column
          prop="num"
          label="序号"
          sortable
          align="center"
          width="150"
        >
        </el-table-column>
        <el-table-column
          prop="min"
          label="工资下限"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="max"
          label="工资上限"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="taxrate"
          label="对应税率"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="updatedate"
          label="更新时间"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="threshold"
          label="征税基数"
          align="center">
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 40]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="taxrateList.length"
        ></el-pagination>
      </div>
    </div>

    <!-- 添加弹框 -->
    <div v-if="flag" class="addTaxrate"  ref="addTaxrate" :style="addTaxrateStyle">
      <div class="title">
        <h2>新增税率管理信息</h2>
        <i class="el-icon-close" @click="closeAdd()"></i>
      </div>

      <div class="add">
        <div class="topButton">
          <el-button type="primary" @click="AddForm()" style="padding: 12px 15px;float: left;position: relative;left:73px;top:-40px">增加记录</el-button>
        </div>
        <el-form ref="form" :model="addList" label-width="80px">
          <span style="font-weight: bold;color:black;font-size: 15px;margin: 50px;position:relative;left: -20px;top:-35px">序号</span >
          <span style="font-weight: bold;color:black;font-size: 15px;margin: 50px;position:relative;left: -20px;top:-35px">工资下限</span >
          <span style="font-weight: bold;color:black;font-size: 15px;margin: 50px;position:relative;left: -20px;top:-35px">工资上限</span>
          <span style="font-weight: bold;color:black;font-size: 15px;margin: 50px;position:relative;left: -20px;top:-35px">对应税率</span>
          <el-form :inline="true" class="demo-form-inline" v-for="(item, i) in FormArr" :key="i">
            <i  style="margin-right: 10px;position:relative;top:-27px;left:-5px;">{{i+1}}</i>
            <el-input v-model="item.min" style="width: 150px;margin-right: 10px;position:relative;top:-25px;left: 50px;"></el-input>
            <el-input v-model="item.max" style="width: 150px;margin-right: 10px;position:relative;top:-25px;left: 50px;"></el-input>
            <el-input v-model="item.taxrate" style="width: 150px;margin-right: 10px;position:relative;top:-25px;left: 50px;"></el-input>
            <i class="el-icon-delete-solid" @click="Delete(item.index)" style="position:relative;top:-25px;left: 50px;"></i>
               
          </el-form>

          <el-form-item label="起征税点:" style="position:relative;left:100px;top:-10px;margin-right: 10px;">
            <el-input prop="threshold" v-model="addList.threshold" style="width: 150px;left:12px;float: left" ></el-input>
          </el-form-item>
          <el-form-item label="更新时间:" style="position:relative;right:-428px;top:-72px">
            <el-date-picker
              v-model="addList.updatedate"
              type="date"
              style="width:150px;left:12px;float:left"
            >
            </el-date-picker>
          </el-form-item>
          <div class="addButton">
            <el-form-item>
              <el-button type="primary" @click="addTaxrate()" style="padding: 12px 15px">添加</el-button>
              <el-button @click="addRest()" style="padding: 12px 15px">重置</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>

    <!-- 修改弹框 -->
    <div v-if="flag2" class="changeTaxrate" ref="changeTaxrate" :style="changeTaxrateStyle">
      <div class="title">
        <h2>修改税率管理信息</h2>
        <i class="el-icon-close" @click="closeChange()"></i>
      </div>

      <div class="change">
        <div class="topButton">
          <el-button type="primary" @click="AddForm1()" style="padding: 12px 15px;float: left;position: relative;left:73px;top:-40px">增加记录</el-button>
        </div>
        <el-form ref="form" :model="changeList" label-width="80px">
          <span style="font-weight: bold;color:black;font-size: 15px;margin: 50px;position:relative;left: -20px;top:-35px">序号</span >
          <span style="font-weight: bold;color:black;font-size: 15px;margin: 50px;position:relative;left: -20px;top:-35px">工资下限</span >
          <span style="font-weight: bold;color:black;font-size: 15px;margin: 50px;position:relative;left: -20px;top:-35px">工资上限</span>
          <span style="font-weight: bold;color:black;font-size: 15px;margin: 50px;position:relative;left: -20px;top:-35px">对应税率</span>
          <el-form :inline="true" class="demo-form-inline" v-for="(item, i) in this.taxrateList" :key="i">
            <i  style="margin-right: 10px;position:relative;top:-27px;left:-5px;">{{i+1}}</i>
            <el-input v-model="item.min" style="width: 150px;margin-right: 10px;position:relative;top:-25px;left: 50px;"></el-input>
            <el-input v-model="item.max" style="width: 150px;margin-right: 10px;position:relative;top:-25px;left: 50px;"></el-input>
            <el-input v-model="item.taxrate" style="width: 150px;margin-right: 10px;position:relative;top:-25px;left: 50px;"></el-input>
            <i class="el-icon-delete-solid" @click="Delete1(item.index)" style="position:relative;top:-25px;left: 50px;"></i>
               
          </el-form>

            <el-form-item label="起征税点:" style="position:relative;left:100px;top:-10px;margin-right: 10px;">
              <el-input prop="threshold" v-model="taxrateList[0].threshold" style="width: 150px;left:12px;float: left" ></el-input>
            </el-form-item>
            <el-form-item label="更新时间:" style="position:relative;right:-428px;top:-72px">
              <el-date-picker
                v-model="taxrateList[0].updatedate"
                type="date"
                style="width:150px;left:12px;float:left"
              >
              </el-date-picker>
            </el-form-item>
          <div class="saveButton">
            <el-form-item>
              <el-button type="primary" @click="changeTaxrate()" style="padding: 12px 15px">保存</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>

    <!-- 删除弹框 -->
    <div class="delete" v-if="flag3">
      <div class="title">
        <span>删除提示</span>
        <i class="el-icon-close" @click="closeDelete()"></i>
      </div>
      <div class="main">
        <i class="el-icon-warning"></i>
        <span>您确定要删除此税率管理信息吗？</span>
      </div>
      <div class="deleteButton">
        <el-button  type="danger" @click="deleteInfo()" style="padding: 12px 15px">确定</el-button>
        <el-button  @click="closeDelete()" style="padding: 12px 15px">取消</el-button>
      </div>
    </div>

    <div class="popContainer" v-if="flag"></div>
    <div class="popContainer" v-if="flag2"></div>
    <div class="popContainer" v-if="flag3"></div>

  </div>
</template>

<script>
    export default {
        data() {
            return {
                taxrateList: [],
                flag: false,
                flag2: false,
                flag3: false,
                currentPage: 1, //初始页
                pagesize: 10,
                flag: false,
                addTaxrateStyle:{
                    height:'355px'
                },
                changeTaxrateStyle:{
                    height:'610px'
                },
                addList: [
                    {
                        id:"",
                        updatedate:"",
                        min:"",
                        max:"",
                        threshold: "",
                        taxrate: ""
                    }
                ],
                FormArr: [
                    {
                        index: 0,
                        value: ''
                    }
                ],
                changeList: [
                    {
                        id:"",
                        updatedate:"",
                        min:"",
                        max:"",
                        threshold: "",
                        taxrate: ""
                    }
                ]
            };
        },
        created(){
            let _this = this;
            this.$axios
                .get("/getAllTaxrate")
                .then(function(res) {
                    if (res.data) {
                        _this.taxrateList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
        },
        inject: ["reload"],
        methods: {
            addTaxrate() {
                let _this = this
                let date =new Date(this.addList.updatedate);
                this.updatedate =date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate();
                for(let i =0;i<this.FormArr.length;i++) {
                    this.$axios
                        .post('/addTaxrate', {
                            min: this.FormArr[i].min,
                            max: this.FormArr[i].max,
                            taxrate: this.FormArr[i].taxrate,
                            threshold: this.addList.threshold,
                            updatedate:this.updatedate,
                        })
                        .then(successResponse => {
                            if (successResponse.data.code === 200) {
                                this.flag = !this.flag;
                                this.reload();
                                this.$message({
                                    message: '添加成功！！！',
                                    type: 'success',
                                    center: true
                                });
                            } else {
                                this.reload();
                                this.$message.error('添加失败！！！');
                            }
                        })
                        .catch(failResponse => {
                            this.reload();
                            this.$message.error('添加信息填写不正确！');
                        });
                }
            },

            changeTaxrate() {
                let _this = this
                this.$axios
                    .put('/deleteTaxrate')
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            for(let i =0;i<this.taxrateList.length;i++) {
                                let date =new Date(this.taxrateList[0].updatedate);
                                this.updatedate =date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate();
                                this.$axios
                                    .post('/addTaxrate', {
                                        min: this.taxrateList[i].min,
                                        max: this.taxrateList[i].max,
                                        taxrate: this.taxrateList[i].taxrate,
                                        threshold: this.taxrateList[0].threshold,
                                        updatedate: this.updatedate,
                                    })
                                    .then(successResponse => {
                                        if (successResponse.data.code === 200) {
                                            this.flag2 = !this.flag2;
                                            this.reload();
                                            this.$message({
                                                message: '修改成功！！！',
                                                type: 'success',
                                                center: true
                                            });
                                        } else {
                                            this.reload();
                                            this.$message.error('修改失败！');
                                        }
                                    })
                                    .catch(failResponse => {
                                        this.reload();
                                        this.$message.error('修改后信息填写不正确！');
                                    });
                            }
                        }else {
                            this.reload();
                            this.$message.error('修改失败！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('修改失败！');
                    });
            },
            deleteInfo() {
                let _this = this
                this.$axios
                    .put('/deleteTaxrate')
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            this.flag3 = !this.flag3;
                            this.reload();
                            this.$message({
                                message: '删除成功！！！',
                                type: 'success',
                                center: true
                            });
                        }else {
                            this.reload();
                            this.$message.error('删除失败！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('删除失败！');
                    });
            },


            showEdit() {
                let _this = this;
                if(this.taxrateList.length === 0){
                    this.flag = !this.flag;
                }else {
                    this.flag2 = !this.flag2;
                }
            },

            showDelete() {

                this.flag3 = !this.flag3;
            },

            closeAdd() {
                this.flag = !this.flag;
                this.$message("操作已取消");
                this.reload();
            },

            AddForm () {
                this.FormArr.push({
                    index: this.FormArr.length,
                    value: ''
                })
                let height= this.$refs.addTaxrate.offsetHeight;
                this.addTaxrateStyle.height =(height +40)+'px';
                console.log(this.FormArr)
            },
            AddForm1 () {
                this.taxrateList.push({
                    index: this.taxrateList.length,
                    value: ''
                })
                console.log(this.taxrateList)
                let height= this.$refs.changeTaxrate.offsetHeight;
                this.changeTaxrateStyle.height =(height +40)+'px';
            },
            Delete (index) {
                this.FormArr.splice(index, 1)
                for (let i in this.FormArr) {
                    this.FormArr[i].index = i
                }
                let height= this.$refs.addTaxrate.offsetHeight;
                this.addTaxrateStyle.height =(height -40)+'px';
            },
            Delete1 (index) {
                this.taxrateList.splice(index, 1)
                for (let i in this.taxrateList) {
                    this.taxrateList[i].index = i
                }
                let height= this.$refs.changeTaxrate.offsetHeight;
                this.changeTaxrateStyle.height =(height -40)+'px';
            },

            addRest() {
                this.addList = [];
                this.FormArr = [
                    {
                        index: 0,
                        value: ''
                    }
                ];
            },

            closeChange() {
                this.flag2 = !this.flag2;
                this.$message("操作已取消");
                this.reload();
            },
            closeDelete() {
                this.flag3 = !this.flag3;
                this.$message("操作已取消");
                this.reload();
            },

            handleCurrentsChange(val) {
                this.currentRow = val;
                this.addList = val;
                this.changeList = val;
            },

            handleSizeChange: function(size) {
                this.pagesize = size;
                // console.log(this.pagesize)  //每页下拉显示数据
            },
            handleCurrentChange: function(currentPage) {
                this.currentPage = currentPage;
                // console.log(this.currentPage)  //点击第几页
            }
        },

    }
</script>
<style>
  .topButton {
    width: 100%;
    height: 50px;
    line-height: 50px;
    padding-bottom: 10px;
  }
  .el-submenu .el-menu-item:hover {
    background-color: rgb(232, 243, 255);
  }
  .list {
    position: relative;
  }
  .page {
    position: absolute;
    right: 0;
    bottom: -50px;
  }

  .el-table--border:after,
  .el-table--group:after,
  .list .el-table:before {
    background-color: #3a8ee6;
  }

  .el-table--border,
  .el-table--group {
    border-color: #3a8ee6;
  }

  .addTaxrate {
    position: absolute;
    z-index: 1001;
    width: 800px;
    /*height: 660px;*/
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  .addTaxrate .title {
    height: 50px;
    width: 100%;
    text-align: center;
    line-height: 30px;
    /* border-bottom: 1px solid #dcdfe6; */
    color: #409eff;
    text-shadow: 2px 2px 2px #ccc;
  }
  .addTaxrate .title i {
    position: relative;
    top: -55px;
    right: -380px;
    color: black;
    font-size: 20px;
  }
  .addTaxrate .el-form-item__label{
    color: black;
    font-weight: bold;
  }
  .addTaxrate .add {
    margin-right: 30px;
    margin-top: 30px;
  }
  .addTaxrate .addButton {
    position: relative;
    left: -90px;
    margin-top: -50px;
  }
  .addTaxrate .addButton .el-button {
    width: 80px;
  }
  .addTaxrate .el-select {
    width: 280px;
  }
  .changeTaxrate {
    position: absolute;
    z-index: 1001;
    width: 800px;
    /*height: 610px;*/
    /* border:1px solid #dcdfe6; */
    left: 0;
    top: 0px;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  .changeTaxrate .title {
    height: 50px;
    width: 100%;
    text-align: center;
    line-height: 30px;
    /* border-bottom: 1px solid #dcdfe6; */
    color: #409eff;
    text-shadow: 2px 2px 2px #ccc;
  }
  .changeTaxrate .title i {
    position: relative;
    top: -55px;
    right: -375px;
    color: black;
    font-size: 20px;
  }
  .changeTaxrate .el-form-item__label{
    color: black;
    font-weight: bold;
  }
  .changeTaxrate .change {
    margin-right: 30px;
    margin-top: 30px;
  }

  .changeTaxrate .saveButton {
    margin-left:-30px;
    margin-top: 0px;
    position: relative;
    top:-80px;
  }

  .changeTaxrate .saveButton .el-button {
    width: 80px;
  }
  .changeTaxrate .el-select {
    width: 280px;
  }
  .changeTaxrate .el-table:before {
    background-color: rgba(141,141,141,0);
  }
  .delete {
    position: absolute;
    z-index: 1001;
    width: 400px;
    height: 180px;
    border: 1px solid #dcdfe6;
    left: 0;
    top: -50px;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
  }
  .title {
    width: 100%;
    height: 45px;
    line-height: 45px;
  }
  .title span {
    float: left;
    margin-left: 20px;
  }
  .delete .title i {
    float: right;
    margin-top: 10px;
    margin-right: 10px;
  }
  .delete .main {
    height: 60px;
    width: 100%;
    line-height: 50px;
    margin-top: 15px;
    margin-left: 0px;
  }
  .delete .main i {
    color: #e6a23c;
    font-size: 20px;
    margin-right: 10px;
  }
  .delete .main span {
    margin-bottom: 5px;
  }
  .delete .deleteButton {
    float: right;
    margin-right: 20px;
    margin-top: 5px;
  }
  .popContainer {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 500;
  }
</style>




