<template>
  <div class="workday">
  <!-- 表格 -->
  <div class="list">
  <el-table
    :data="workdayList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
    style="width: 100%;border-color: #3a8ee6;"
    :default-sort = "{prop: 'time', order: 'descending'}"
    border
    :header-cell-style="{background:'#eef1f6',color:'#333333'}"
    @current-change="handleCurrentsChange"
  >
    <el-table-column
      prop="time"
      label="时间"
      sortable
      align="center"
    >
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      align="center">
    </el-table-column>
    <el-table-column
      prop="absence"
      label="请假天数（小时）"
      align="center"
      sortable>
    </el-table-column>
    <el-table-column
      prop="overtimen"
      label="普通加班（小时）"
      align="center"
      sortable>
    </el-table-column>
    <el-table-column
      prop="overtimed"
      label="假日加班（小时）"
      align="center"
      sortable>
    </el-table-column>
    <el-table-column
      prop="absenteeism"
      label="旷工"
      align="center"
      sortable>
    </el-table-column>
    <el-table-column
      prop="late"
      label="迟到"
      align="center"
      sortable>
    </el-table-column>
    <el-table-column
      prop="leaveral"
      label="早退"
      align="center"
      sortable>
    </el-table-column>
    <el-table-column
      prop="status"
      label="状态"
      align="center"
      sortable>
      <template scope="scope">
        <el-tag
          :type="scope.row.status === '未提交' ? 'primary' : 'success'"
          disable-transitions>{{scope.row.status}}</el-tag>
      </template>

    </el-table-column>
  </el-table>
  <!-- 分页 -->
  <div class="page">
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 20, 40]"
      :page-size="pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="workdayList.length"
    ></el-pagination>
  </div>
  </div>
  </div>
</template>
<script>

    export default {
        data() {
            return {
                workdayList: [],
                currentPage: 1, //初始页
                pagesize: 10,
                flag: false,
            };
        },
        computed: {
            eid() {
                return this.$store.state.employer.id;
            }
        },
            created(){
                let _this = this;
                this.$axios
                    .get("/getWorkdayByEid?eid="+this.eid)
                    .then(function(res) {
                        if (res.data) {
                            _this.workdayList = res.data;
                        }
                    })
                    .catch(function(err) {
                        if (err.response) {
                            console.log(err.response);
                        }
                    });
            },
        methods: {
            handleCurrentsChange(val) {
                this.currentRow = val;

                this.changeDept = val;
                console.log(this.changeDept)
                // this.changeList = []
            },
            handleSizeChange: function(size) {
                this.pagesize = size;
                // console.log(this.pagesize)  //每页下拉显示数据
            },
            handleCurrentChange: function(currentPage) {
                this.currentPage = currentPage;
                // console.log(this.currentPage)  //点击第几页
            }
        },
    }
</script>
<style>
  .list {
    position: relative;
  }
  .page {
    /* margin-top: 20px;
      margin-left: 800px; */
    position: absolute;
    right: 0;
    bottom: -50px;
  }
  .el-table--border:after,
  .el-table--group:after,
  .el-table:before {
    background-color: #3a8ee6;
  }

  .el-table--border,
  .el-table--group {
    border-color: #3a8ee6;
  }

</style>



