import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    accountant: {
      name: window.localStorage.getItem('accountant' || '[]') == null ? '' : JSON.parse(window.localStorage.getItem('accountant' || '[]')).name
    },
    employer: {
      name: window.localStorage.getItem('employer' || '[]') == null ? '' : JSON.parse(window.localStorage.getItem('employer' || '[]')).name,
      id: window.localStorage.getItem('employer' || '[]') == null ? '' : JSON.parse(window.localStorage.getItem('employer' || '[]')).id
    }
  },
  mutations: {
    aLogin (state, accountant) {
      state.accountant = accountant
      window.localStorage.setItem('accountant', JSON.stringify(accountant))
    },
    eLogin (state, employer) {
      state.employer = employer
      window.localStorage.setItem('employer', JSON.stringify(employer))
    }
  }
})



