<template>
  <body id = "poster">
    <el-form class="login-container" label-position="left"
                       label-width="0px">
    <h3 class="login_title">员工登录</h3>
    <el-form-item>
      <el-input type="text" v-model="loginForm.id"
                auto-complete="off" placeholder="工号"></el-input>
    </el-form-item>
    <el-form-item>
      <el-input type="text" v-model="loginForm.name"
                auto-complete="off" placeholder="姓名"></el-input>
    </el-form-item>
    <el-form-item style="width: 100%">
      <el-button type="primary" style="width: 30%;background: #505458;border: none" v-on:click="login">登录</el-button>
      <el-button type="primary" style="width:30%;background: #505458;border: none" v-on:click="exit">返回</el-button>
    </el-form-item>
  </el-form>
  </body>
</template>

<script>

  export default {
    name: 'eLogin',
    data () {
      return {
        loginForm: {
          id: '',
          name: ''
        },
        responseResult: []
      }
    },
    methods: {
        exit() {
            //指定跳转地址
            this.$router.replace({path: '/login'});
        },
        login() {
            this.id = this.loginForm.id;
            this.name = this.loginForm.name;
            if (this.id.length === 0 | this.name.length === 0) {
                this.$message({
                    message: '工号或姓名不能为空！',
                    type: 'warning'
                });
            } else {
                var _this = this
                console.log(this.$store.state)
                this.$axios
                    .post('/eLogin', {
                        id: this.loginForm.id,
                        name: this.loginForm.name
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            _this.$store.commit('eLogin', _this.loginForm)
                            var path = this.$route.query.redirect
                            this.$router.replace({path: path === '/' || path === undefined ? '/eIndex' : path})

                            this.$message({
                                message: '登陆成功！！！',
                                type: 'success',
                                center: true
                            });
                        } else {
                            this.$message.error('登陆失败，员工工号或者姓名错误！');
                        }
                    })
                    .catch(failResponse => {

                    })
            }
        }
    }
  }
</script>
<style>
  .login-container {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 90px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }

  .login_title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }

  #poster {
    background:url("../assets/background.jpg") no-repeat;
    background-position: top;
    height: 100%;
    width: 100%;
    background-size: cover;
    position: fixed;
  }
  body{
    margin: 0px;
  }

</style>



