<template>
  <div class="total" >
    <div class="topButton">
      <el-button type="primary" @click="showAdd()" style="padding: 12px 15px;float: left">新建本月记录</el-button>
    </div>
    <!-- 表格 -->
    <div class="list">
      <el-table
        :data="totalList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        style="width: 100%;border-color:#3a8ee6"
        :default-sort = "{prop: 'num', order: 'ascending'}"
        border
        :header-cell-style="{background:'#eef1f6',color:'#333333'}"
        @current-change="handleCurrentsChange"
      >
        <el-table-column
          prop="num"
          label="序号"
          align="center"
          width="150"
        >
        </el-table-column>
        <el-table-column
          prop="time"
          label="时间"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="sum"
          label="工资发放总额"
          align="center">
        </el-table-column>
        <el-table-column
          prop="operate"
          label="操作"
          align="center">
          <template scope="scope" >
            <el-button type="primary" @click="showDetail(scope.row.time)" style="padding: 12px 15px">查看</el-button>
            <el-button type="danger"  @click="showDelete()" style="padding: 12px 15px">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 40]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalList.length"
        ></el-pagination>
      </div>
    </div>

    <!-- 添加弹框 -->
    <div v-if="flag" class="addTotal">
      <div  class="addTitle">
        <h2>新建工资单</h2>
        <i class="el-icon-close" @click="closeAdd()"></i>
      </div>

      <div class="add">
        <el-form ref="form" :model="addList" label-width="150px">
          <el-form-item label="选择工资发放时间：" style="position: relative;left: 85px;size: 15px">
            <el-select v-model="addList.time" placeholder="请选择时间" style="width:180px;position: relative;left: -55px;">
              <el-option  v-for="item in createdateList"
                          :label="item.name"
                          :value="item.name"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <div class="addButton">
            <el-form-item>
              <el-button type="primary" @click="addTotal()" :loading="loadingbut" style="position: relative;left: -20px;padding: 12px 15px">{{loadingbuttext}}</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>

    <!-- 删除弹框 -->
    <div class="delete" v-if="flag3">
      <div class="title">
        <span>删除提示</span>
        <i class="el-icon-close" @click="closeDelete()"></i>
      </div>
      <div class="main">
        <i class="el-icon-warning"></i>
        <span>您确定要删除此工资管理信息吗？</span>
      </div>
      <div class="deleteButton">
        <el-button  type="danger" @click="deleteInfo()" style="padding: 12px 15px">确定</el-button>
        <el-button  @click="closeDelete()" style="padding: 12px 15px">取消</el-button>
      </div>
    </div>

    <div class="popContainer" v-if="flag"></div>
    <div class="popContainer" v-if="flag2"></div>
    <div class="popContainer" v-if="flag3"></div>

  </div>
</template>

<script>
    export default {
        data() {
            return {
                loadingbut:false,//审核提交加载中
                loadingbuttext:'添加',
                totalList: [],
                flag: false,
                flag2: false,
                flag3: false,
                currentPage: 1, //初始页
                pagesize: 10,
                flag: false,
                addList: [
                    {
                        id:"",
                        time:"",
                        sum: ""
                    }
                ],
                createdateList:[

                ],
                FormArr: [
                    {
                        index: 0,
                        value: ''
                    }
                ],
                deleteList: [
                    {
                        id:""
                    }
                ],

            };
        },
        mounted(){

        },
        created(){
            let _this = this;
            this.$axios
                .get("/getAllTotal")
                .then(function(res) {
                    if (res.data) {
                        _this.totalList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
            this.$axios
                .get("/getAllCreatedate")
                .then(function(res) {
                    if (res.data) {
                        _this.createdateList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
        },
        inject: ["reload"],
        methods: {
            addTotal() {
                let _this = this
                this.loadingbut = true;
                this.loadingbuttext = '新建中...';
                this.$axios
                    .post('/addTotal', {
                        saltime: this.addList.time
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            this.flag = !this.flag;
                            this.reload();
                            this.$message({
                                message: '新建成功！！！',
                                type: 'success',
                                center: true
                            });
                        } else if(successResponse.data.code === 100){
                            this.reload();
                            this.$message.error('新建失败！未找到请假加班信息！');
                        } else if(successResponse.data.code === 300){
                            this.reload();
                            this.$message.error('新建失败！该月记录已存在！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('新建失败！');
                    });
            },

            deleteInfo() {
                let _this = this
                this.$axios
                    .delete('/deleteTotal', {
                        data: {
                            id:this.deleteList.id
                        }
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            this.flag3 = !this.flag3;
                            this.reload();
                            this.$message({
                                message: '删除成功！！！',
                                type: 'success',
                                center: true
                            });
                        }else {
                            this.reload();
                            this.$message.error('删除失败！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('删除失败！');
                    });
            },


            showAdd() {
                this.flag = !this.flag;
                this.addList = [];
            },

            showDetail(time) {
                //指定跳转地址
                this.$router.push({
                    path: "/salaryManageDetail",
                    query: { time: time}
                })
            },

            showDelete() {
                this.flag3 = !this.flag3;
            },

            closeAdd() {
                this.flag = !this.flag;
                this.$message("操作已取消");
                this.reload();
            },

            AddForm () {
                this.FormArr.push({
                    index: this.FormArr.length,
                    value: ''
                })
                console.log(this.FormArr)
            },
            Delete (index) {
                this.FormArr.splice(index, 1)
                for (let i in this.FormArr) {
                    this.FormArr[i].index = i
                }
            },

            closeDelete() {
                this.flag3 = !this.flag3;
                this.$message("操作已取消");
                this.reload();
            },

            handleCurrentsChange(val) {
                this.currentRow = val;
                this.addList = val;
                this.changeList = val;
                this.deleteList = val;
            },
            handleSizeChange: function(size) {
                this.pagesize = size;
                // console.log(this.pagesize)  //每页下拉显示数据
            },
            handleCurrentChange: function(currentPage) {
                this.currentPage = currentPage;
                // console.log(this.currentPage)  //点击第几页
            }
        },

    }
</script>
<style>
  .topButton {
    width: 100%;
    height: 50px;
    line-height: 50px;
    padding-bottom: 10px;
  }
  .el-submenu .el-menu-item:hover {
    background-color: rgb(232, 243, 255);
  }
  .list {
    position: relative;
  }
  .page {
    position: absolute;
    right: 0;
    bottom: -50px;
  }

  .el-table--border:after,
  .el-table--group:after,
  .el-table:before {
    background-color: #3a8ee6;
  }

  .el-table--border,
  .el-table--group {
    border-color: #3a8ee6;
  }

  .addTotal {
    position: absolute;
    z-index: 1001;
    width: 500px;
    height: 240px;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  .addTotal .addTitle {
    height: 50px;
    width: 100%;
    text-align: center;
    line-height: 30px;
    /* border-bottom: 1px solid #dcdfe6; */
    color: #409eff;
    text-shadow: 2px 2px 2px #ccc;
  }
  .addTotal .addTitle i {
    position: relative;
    top: -55px;
    right: -220px;
    color: black;
    font-size: 20px;
  }
  .addTotal .el-form-item__label{
    color: black;
    font-weight: bold;
  }
  .addTotal .add {
    margin-right: 30px;
    margin-top: 30px;
  }
  .addTotal .addButton {
    margin-left:-80px;
    margin-top: 30px;
  }
  .addTotal .addButton .el-button {
    width: 100px;
  }
  .addTotal .el-select {
    width: 280px;
  }

  .el-form-item__label{
    font-size: 15px;
  }
  .delete {
    position: absolute;
    z-index: 1001;
    width: 400px;
    height: 180px;
    border: 1px solid #dcdfe6;
    left: 0;
    top: -50px;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
  }
  .title {
    width: 100%;
    height: 45px;
    line-height: 45px;
  }
  .title span {
    float: left;
    margin-left: 20px;
  }
  .delete .title i {
    float: right;
    margin-top: 10px;
    margin-right: 10px;
  }
  .delete .main {
    height: 60px;
    width: 100%;
    line-height: 50px;
    margin-top: 15px;
    margin-left: 0px;
  }
  .delete .main i {
    color: #e6a23c;
    font-size: 20px;
    margin-right: 10px;
  }
  .delete .main span {
    margin-bottom: 5px;
  }
  .delete .deleteButton {
    float: right;
    margin-right: 20px;
    margin-top: 5px;
  }
  .popContainer {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 500;
  }
</style>



